<template>
    <div class="order">
      <van-nav-bar
    title="收货地址"
    left-arrow
    @click-left="onClickLeft"
  />

      <div class="content">
        <div v-if="minLength">
    <van-address-edit
        :area-list="areaList"
        show-search-result
        show-set-default
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        :address-info="AddressInfo"
        @save="onSave"
        @delete="onDelete"
        />
  </div>
  <div v-else>
    <van-address-edit
        :area-list="areaList"
        show-search-result
        show-set-default
        show-delete
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        :address-info="AddressInfo"
        @save="onSave"
        @delete="onDelete"
        />
  </div>
      </div>
    </div>
  </template>
    
