        this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });

   
      var mor =  _this.AddressInfo.isDefault ? '1':'0'
      var list =  _this.AddressInfo.areaCode+','+ _this.AddressInfo.province+','+ _this.AddressInfo.city+','+ _this.AddressInfo.county+','+ _this.AddressInfo.addressDetail+','+ _this.AddressInfo.name+','+ _this.AddressInfo.tel+','+mor+','+'0';

      console.log(list);

        this.getListContract.methods
        .removeAddress(list,_this.oldIndex)
        .send({
          from: account,
          value:0
        })
        .then((res) => {
          this.$toast.success('删除成功！');
          this.$toast.clear();
          this.hreftwo('/addressList');
        });
      },
