    //           }
    //         }).then(
    //         function(res){
    //           var data = res.data
    //           if(data.success==true){
    //             _that.$toast.success('保存成功！');
    //             _that.hreftwo('/addressList');
    //           }
            
    //         }
    //       ).catch(
    //         function(err){
    //           console.log(err)
    //         }
    //       )


          
    //  }else{
    //  }

    if(!_that.minLength){

      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var mor = val.isDefault ? '1':'0'
      var list = val.areaCode+','+val.province+','+val.city+','+val.county+','+val.addressDetail+','+val.name+','+val.tel+','+mor+','+'1';
        console.log(list);
      this.getListContract.methods
        .removeAddress(list,_that.oldIndex)
        .send({
          from: account,
          value:0
        })
        .then((res) => {
          this.$toast.success('添加成功！');
          this.$toast.clear();
          this.hreftwo('/addressList');
        });
    }else{
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var mor = val.isDefault ? '1':'0'
      var list = val.areaCode+','+val.province+','+val.city+','+val.county+','+val.addressDetail+','+val.name+','+val.tel+','+mor+','+'1';
        console.log(list);
      this.getListContract.methods
        .addAddress(list)
        .send({
          from: account,
          value:0
        })
        .then((res) => {
          this.$toast.success('添加成功！');
          this.$toast.clear();
          this.hreftwo('/addressList');
        });

    }
      
    },
    },
    mounted() {
    //   var token = localStorage.getItem('accessToken');
