    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var id=this.$route.query.id;
    if(!id){
      _this.minLength = true;
    }
    var getData = () => {
      this.add_getListContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.getListContract || !this.usdtCoinContract) {
          getData();
        } else {

          _this.listData.forEach((element,index) => {
            var address=element.split(",");
            if(address[0]==id){
              _this.oldIndex = index;
              _this.old = element;

              _this.AddressInfo={
                addressDetail: address[4],
                areaCode: address[0],
                city: address[3],
                county:address[2],
                name: address[5],
                province:address[1],
                tel: address[6],
                id:address[0],
                isDefault:address[7]==1?true:false,
                index:index
              }
            }
            
            console.log( _this.AddressInfo)
            
          });

          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // // 每三十秒，刷新一次
    // this.timer2 = setInterval(() => {
    //   if (this.timer) {
    //     clearInterval(this.timer);
    //     this.timer = null;
    //   }
    //   getDataCalculate();
