    },
    destroyed() {
      if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },

  };
  </script>
    
    <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped  lang="less">
  .order{
    .van-address-list{
      padding: 12px 12px 20px !important;
    }
.van-address-edit__fields{
      background: #fff !important;
    }
  }
  .content {
    padding-top: 1px;

    .van-card{
      background: #fff !important;
      padding:20px 16px !important;
      margin-top: 10px;
    }
    .goods{
      p{
         background: #fff;
      border-top: 1px solid #f5f5f5;
      padding: 15px 16px;
      color: #999999;
      span{
        float: right;
        color:#000;
      }
      }
     .mini{
      padding: 15px 16px;
      background: #fff;
      margin-top:10px;
      text-align: right;
      color: #999999;
      span{
        float: right;
        color:#000;
        font-size: 16px;
        margin-left: 10px;
      }
     }
    }
  }
  .card{ 
    background: #fff;
    padding: 10px;
    margin-top: 10px;
    img{width: 100%;border-radius: 5px;}
    .box{
     
      h2{
        font-size: 16px;
        font-weight: normal;
        color:#333333;
        padding-top: 10px;
      }
      h3{
        font-size: 14px;
        font-weight: normal;
        color:#999;
        padding-top: 5px;
      }
      h1{
        font-size: 18px;
        font-weight: normal;
        color:#ff4a6f;
        padding-top: 20px;
        span{
          float: right;
          font-size: 14px;
          color:#999;
        }
      }
    }
    .info{
      border-top: 1px solid #f5f5f5;
      padding: 10px;
      margin-top: 5px;
      p{
      font-size: 15px;
      color:#333;
      text-align: right;
      margin-top: 5px;
    }
    } 
  }
  </style>
    
  
  


