// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueI18n from 'vue-i18n'
import * as filters from './filters/filters.js'
import axios from 'axios'
// import VueAxios from 'vue-axios';


import LangEn from '../static/lang/en'
import LangZhCHS from '../static/lang/zhCHS'
import LangZhCHT from '../static/lang/zhCHT'


import store from "./store";

import Vant from 'vant';
import 'vant/lib/index.css';

  
import { AddressEdit } from 'vant';
import { AddressList } from 'vant';
import { SubmitBar } from 'vant';
import { Form } from 'vant';
import { Field } from 'vant';
import { Card } from 'vant';
import { Tag } from 'vant';
import { Tabbar, TabbarItem } from 'vant';
import { Cell, CellGroup } from 'vant';
import VueClipboard from 'vue-clipboard2';
import { Search } from 'vant';
import { Tab, Tabs } from 'vant';
import { PullRefresh } from 'vant';
import { Step, Steps } from 'vant';
import { NavBar } from 'vant';
import { GoodsAction, GoodsActionIcon, GoodsActionButton } from 'vant';
import { ActionSheet } from 'vant';
import { Stepper } from 'vant';
import { NoticeBar } from 'vant';
import { Dialog } from 'vant';


Vue.use(VueClipboard)
Vue.use(AddressEdit);
Vue.use(AddressList);
Vue.use(Tag);
Vue.use(Card);
Vue.use(Form);
Vue.use(Field);
Vue.use(SubmitBar);
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Search);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(PullRefresh);
Vue.use(Step);
Vue.use(Steps)
Vue.use(NavBar);
Vue.use(GoodsAction);
Vue.use(GoodsActionButton);
Vue.use(GoodsActionIcon);
Vue.use(ActionSheet);
Vue.use(Stepper);
Vue.use(NoticeBar);
Vue.use(Dialog);
Vue.prototype.$axios = axios
Vue.use(Vant);
Vue.prototype.ArrList={
    dappArr:"0x32f785bbb86b9e72f71f6860762a6861c6462ea8",
    UsdtArr:"0x55d398326f99059fF775485246999027B3197955",
    FiveArr:"0xd47adda2a0491fda35892ddc0bc85bf01adec8d1",//gns
    BuyArr:"0xca2b54858faffd333b866ec45a94dc9ee181f7ea",//buy
    XhArr:"0x000000000000000000000000000000000000dEaD",
    AesLpArr:"0xe068cc60fd1c4b13bcd6b39de69158b23f22c966",//NFT
    address:"0x29ad94727197124baff6a4ec23762fd01b64f3dc",//收货地址
    paid:"0xd544a2c40ed9a9e6e00c06ce02bc89237d632d98"//收货地址
}

Vue.config.productionTip = false;

Object.keys(filters).forEach(key=>{
    Vue.filter(key,filters[key])//插入过滤器名和对应方法
})

Vue.use(VueI18n);



const i18n = new VueI18n({
    locale: 'zhCHS', // 语言标识
    messages: {
        'en': LangEn,
        'zhCHS': LangZhCHS,
        'zhCHT': LangZhCHT
    }
});



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
});


