import { setStorage, getStorage } from '@/assets/js/units.js';
import getWeb3 from '@/assets/js/getWeb3.js'
import OKTABI from '@/assets/coin_abi/DOT.json'
import HMDABI from '@/assets/coin_abi/HMD.json'
import pool_other from '@/assets/pool_abi/pool_other.json'
import pool_NFT from '@/assets/pool_abi/pool_dao.json'//nft卡牌
import pool_buy from '@/assets/pool_abi/buy.json'//nft卡牌
import paid from '@/assets/pool_abi/paid.json'//nft卡牌
import address from '@/assets/pool_abi/address.json'//nft卡牌
import Vue from 'vue'


const state = {
  /*存储用户信息全局变量*/
  account: JSON.parse(getStorage('account') || 0),
  usdtBalance: JSON.parse(getStorage('usdtBalance') || 0),
  usdtCoinContract: JSON.parse(getStorage('usdtCoinContract') || null),//usdt
  buyContract: JSON.parse(getStorage('buyContract') || null),//购买合约
  userInfo: JSON.parse(getStorage('userInfo') || null),//用户信息
  listData: JSON.parse(getStorage('listData') || null),//地址列表
  getListContract: JSON.parse(getStorage('getListContract') || null),//获得地址合约
  paidContract: JSON.parse(getStorage('paidContract') || null),//地址列表
  paidData: JSON.parse(getStorage('paidData') || null),//获得地址合约
}

const getters = {
  /*获取用户信息*/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {

  set_account: (state, data) => {
    state.account = data;
  },

  // usdt测试币
  set_usdtCoinContract: (state, data) => {
    state.usdtCoinContract = data;
  },

    // 购买商品
    set_buyContract: (state, data) => {
      state.buyContract = data;
    },
    set_usdtBalance: (state, data) => {
      state.usdtBalance = data;
    },
    set_userInfo: (state, data) => {
      state.userInfo = data;
    },
    set_listData:(state, data) => {
      state.listData = data;
    },
    set_getListContract:(state, data) => {
      state.getListContract = data;
    },
    set_paidContract:(state, data) => {
      state.paidContract = data;
    },
    set_paidData:(state, data) => {
      state.paidData = data;
    },
}

const actions = {
  // 团队计酬
  add_paidContract: ({ commit }, data) => {
    getWeb3.Init(paid, Vue.prototype.ArrList.paid, (contract, addr, balance) => {
      commit('set_account', addr);
      commit('set_paidContract', contract)
      contract.methods
      .getUserInfo(addr,1)
      .call()
      .then((res) => {
        console.log(res.ct)
        commit('set_paidData', res.ct)
      })
    })
  },

  // 获取usdt币合约对象
  add_usdtCoinContract: ({ commit }, data) => {
    getWeb3.Init(OKTABI, Vue.prototype.ArrList.UsdtArr, (contract, addr, balance) => {
          // if(addr){
          //   let data = {"access":addr,"password":"-1","code":"-1"}
          //   Vue.prototype.$axios.post('http://42.194.207.81:38080/linkstop/client/link/user/login',data).then(
          //       function(res){
          //         var data = res.data.result.result
          //         localStorage.setItem('accessToken',data.token);
          //         localStorage.setItem('access',data.access)
          //         // if(data.success==true){
          //         //   _this.goods = data.result.result;
          //         // }
          //         console.log(data)
          //       }
          //     )
          // }
          
      contract.methods
        .balanceOf(addr)
        .call()
        .then((res) => {
          commit('set_usdtBalance', res)
        })
      commit('set_account', addr);
      commit('set_usdtCoinContract', contract)
    })
  },
  // 购买商品
   add_buyContract: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(pool_buy, Vue.prototype.ArrList.BuyArr, (contract, addr, balance) => {
      commit('set_account', addr);
      commit('set_buyContract', contract)
      contract.methods
      .getUserInfo(addr,1)
      .call()
      .then((res) => {
        info.info = res[0];
        info.inviteCode = res[1];
        info.referrer = res[2];
        // console.log(info)
        // commit('set_userInfo', info)
      }).then(()=>{
        // five单价
        contract.methods.getNewTokenPrice( Vue.prototype.ArrList.UsdtArr,Vue.prototype.ArrList.FiveArr,'1000000000000000000').call()
        .then((data1) => {
          var prex =4;
          var price = web3.utils.fromWei(data1, 'ether');
          info.fivePrice =null;
          if (parseInt(price) == price){
            info.fivePrice=price;
         
            return 
          } 
          let len = 1;
          let i = 0;
          while (parseInt(price) != price) {
            price = price * 10;
              len *= 10;
              i++;
          }
          let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
          let d1 = price - last;
          if (last > 0) {
              d1 = d1 + Math.pow(10, (i - prex));
          }
          d1 = d1 / len;
         info.fivePrice=d1;
         
        }).then(()=>{
          // AES单价
          contract.methods.getNewTokenPrice(Vue.prototype.ArrList.FiveArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call()
          .then((data) => {
            var prex =4;
            var price = web3.utils.fromWei(data, 'ether');
            // console.log('price======',price)
            info.AESPrice =null;
            var arr = price.split('.');
            price =arr[0]+'.'+arr[1].substring(0,5)
            info.AESPrice = Number(price);
            // console.log(price)
          //   if (parseInt(price) == price){
          //     info.AESPrice=price;
          //     // console.log(info)
          //           commit('set_hmdInfo', info);
          //     return 
          //   } 
          //   let len = 1;
          //   let i = 0;
          //   while (parseInt(price) != price) {
          //     price = price * 10;
          //       len *= 10;
          //       i++;
          //   }
          //   let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
          //   let d1 = price - last;
          //   if (last > 0) {
          //       d1 = d1 + Math.pow(10, (i - prex));
          //   }
          //   d1 = d1 / len;
          //  info.AESPrice=d1;
           commit('set_userInfo', info);
           console.log('set_userInfo=============',info)
          })
        })
      })
    })
  },
  // 获取地址
    add_getListContract: ({ commit }, data) => {
      getWeb3.Init(address, Vue.prototype.ArrList.address, (contract, addr, balance) => {
        commit('set_getListContract', contract)
        contract.methods
        .getAll()
        .call({
          from: addr,
        })
        .then((res) => {
          commit('set_listData', res)
        })
      })
    },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}




