<template>
    <div class="order">
      <van-nav-bar
    title="收货地址"
    left-arrow
    @click-left="onClickLeft"
  />
      <div class="content">
        <van-address-list
          v-model="chosenAddressId"
          :list="list"
          :switchable="false"
          default-tag-text="默认"
          @edit="onEdit"
          @add="onAdd"
        />
      </div>
    </div>
  </template>
    
  <script type="es6">
  import HeadTop from "../../components/Header";
  import { mapActions, mapState } from "vuex";
  import { areaList } from '@vant/area-data';
  export default {
    data() {
      return {
        lang: "en",
        currency: "",
        link: "--",
        areaList,
        chosenAddressId: '',
        timer:null,
        timer2:null,
      list: [],
        minLength:false
      };
    },
    computed: {
      ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "listData",
      "userInfo",
      "getListContract",
      ]),
    },
    components: {
      HeadTop,
    },
    methods: {
      ...mapActions("main", [
      "add_usdtCoinContract",
        "add_getListContract"
      ]),
      changeAppLocale(locale) {
        this.changeLanguage(locale);
      },
      changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
      onClickLeft(){
        // console.log(this.$router)
        this.$router.go(-1)
      },
      hreftwo(url) {
        this.$router.push({ path: url });
      },
      onSave(val) {
        var token = localStorage.getItem('accessToken');
        var _that = this;
        if(_that.minLength){
          var url = "http://42.194.207.81:38080/linkstop//client/link/user/address/update";
        }else{
          var url = "http://42.194.207.81:38080/linkstop//client/link/user/address/add";
        }
        var params ={"bean": {
                      "name": val.name,
                      "phone": val.tel,
                      "provinceId": "0",
                      "cityId": "0",
                      "areaId": "0",
                      "detailAddress": val.areaCode+','+val.province+','+val.city+','+val.county+','+val.addressDetail
                    },
                    "optType": "save"
                  }

        if(token){
            // 查询地址列表接口
            this.$axios.post(url,params,{
              headers:{
                "Authorization":token
              }
            }).then(
            function(res){
              var data = res.data
              if(data.success==true){
                _that.$toast.success('保存成功！');
              }
            
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )


          
     }else{
      // this.add_hmdInfo();
     }
      console.log(val)
    },
    onEdit(item, index) {
      console.log(item, index);
      this.hreftwo('/addressEdit?id='+item.id);
    },
    onAdd(val) {
      this.hreftwo('/addressEdit');
    },
  
    },
    mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_getListContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.getListContract || !this.usdtCoinContract) {
          getData();
        } else {  
          _this.list = [];
          _this.listData.forEach((element,index) => {
            var lists=element.split(",");
            if(lists[8]==1){
              //[8],1显示，0默认不显示
             
            
              _this.list.push({
                id: lists[0],
                name: lists[5],
                tel:  lists[6],
                address: lists[1]+lists[2]+lists[3]+lists[4],
                isDefault: lists[7]=='1'?true:false,
              })
              if(lists[7]=='1'){
                _this.chosenAddressId = index
              }
            }
            
            
          });
          _this.list.forEach((element,index)=>{
            if(element.isDefault){
                _this.chosenAddressId = (index+1);
              }
          })
          console.log(_this.list,_this.chosenAddressId)
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // // 每三十秒，刷新一次
    // this.timer2 = setInterval(() => {
    //   if (this.timer) {
    //     clearInterval(this.timer);
    //     this.timer = null;
    //   }
    //   getDataCalculate();
    // }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  };
  </script>
    
    <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped  lang="less">
  .order{
.van-address-edit__fields{
      background: #fff !important;
    }
    .van-switch--on {
    background-color: #000 !important;
}
  }
  .content {
    padding-top: 1px;

    .van-card{
      background: #fff !important;
      padding:20px 16px !important;
      margin-top: 10px;
    }
    .goods{
      p{
         background: #fff;
      border-top: 1px solid #f5f5f5;
      padding: 15px 16px;
      color: #999999;
      span{
        float: right;
        color:#000;
      }
      }
     .mini{
      padding: 15px 16px;
      background: #fff;
      margin-top:10px;
      text-align: right;
      color: #999999;
      span{
        float: right;
        color:#000;
        font-size: 16px;
        margin-left: 10px;
      }
     }
    }
  }
  .card{ 
    background: #fff;
    padding: 10px;
    margin-top: 10px;
    img{width: 100%;border-radius: 5px;}
    .box{
     
      h2{
        font-size: 16px;
        font-weight: normal;
        color:#333333;
        padding-top: 10px;
      }
      h3{
        font-size: 14px;
        font-weight: normal;
        color:#999;
        padding-top: 5px;
      }
      h1{
        font-size: 18px;
        font-weight: normal;
        color:#ff4a6f;
        padding-top: 20px;
        span{
          float: right;
          font-size: 14px;
          color:#999;
        }
      }
    }
    .info{
      border-top: 1px solid #f5f5f5;
      padding: 10px;
      margin-top: 5px;
      p{
      font-size: 15px;
      color:#333;
      text-align: right;
      margin-top: 5px;
    }
    } 
  }
  </style>
    
  
  


