<template>
    <div class="details">
        <h1 class="title">商品分类</h1>
        <!-- <div class="search">
          <form action="/">
            <van-search
              v-model="value"
              show-action
              action-text="确定"
              placeholder="请输入搜索关键词"
              @search="onSearch"
              @cancel="onCancel"
            />
          </form>
        </div> -->
      <div class="content">
        <van-row  type="flex" justify="space-between" style="height:100%">
          <van-col span="5">
            <ul class="">
              <li :class="tab==1?'in':''" @click="changeTab(1)">热门推荐</li>
              <li :class="tab==2?'in':''" @click="changeTab(2)">健康养生</li>
              <li :class="tab==3?'in':''" @click="changeTab(3)">食品酒水</li>
              <li :class="tab==4?'in':''" @click="changeTab(4)">茶叶饮品</li>
              <li :class="tab==5?'in':''" @click="changeTab(5)">品质生活</li>
            </ul>
          </van-col>
          <van-col span="19">
            <div class="box" v-if="goods">
              <van-row gutter="20" type="flex" justify="space-between">
                <van-col span="12" v-for="(itme,index) in goods" :key="index">
                  <div class="box_1" @click="hreftwo('/details','id='+itme.id)">
                    <img :src="itme.insets" alt="" width="100%" >
                  <p>{{itme.name}}</p>
                  </div>
                  
                </van-col>

            </van-row>
            </div>
            
          </van-col>
        </van-row>
      </div>
      <van-tabbar v-model="active" active-color="#000" inactive-color="#565656">
        <van-tabbar-item @click="hreftwo('/home')"> 
          <span>首 页</span>
          <template slot="icon" >
            <img src="../../assets/img/fan/home1.png" alt="">
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="hreftwo('/classify')">
          <span>分 类</span>
          <template slot="icon" slot-scope="props">
            <img :src="props.active ? icon.active : icon.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="hreftwo('/user')">
          <span>个人中心</span>
          <template slot="icon">
            <img src="../../assets/img/fan/user1.png" alt="">
          </template>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </template>
    
  <script type="es6">
  import HeadTop from "../../components/Header";
  import { mapActions, mapState } from "vuex";
  import { areaList } from '@vant/area-data';
  import data from "../../assets/data.json";
  export default {
    data() {
      return {
        lang: "en",
        currency: "",
        value: '',
        tab:1,
        active: 1,
        goods:[],
        icon: {
        active: 'https://timebabycat.github.io/lei.png',
        inactive: 'https://timebabycat.github.io/lei1.png',
      },
      };
    },
    computed: {
      ...mapState("main", [
        "nftInfo",
        "nftContract",
        "hmdInfo",
        "account",
        "balance",
        "mxtCoinContract",
        "mxtContract",
        "hmdBalance",
        "FiveCoinContract",
        "FiveDead"
      ]),
    },
    components: {
      HeadTop,
    },
    methods: {
      ...mapActions("main", [
        "add_nftContract",
        "add_hmdInfo",
        "add_mxtCoinContract",
        "add_hmdCoinContract"
      ]),
      changeTab(val){
        this.tab = val;
        var list = []
        var _this=this
         
        if(val==1){
          this.$axios.get('https://timenftbsc.github.io/goods.json').then(
          function(res){
            _this.goods = res.data;
            console.log( _this.goods)
            }
          ).catch(
            function(err){
              console.log(err)
            }
            
          )
        }else{

          this.$axios.get('https://timenftbsc.github.io/goods.json').then(
          function(res){
            var dataList = res.data;
            dataList.forEach(element => {
            if(val==element.type ){
              list.push(element)
            }
          
          });
            }
          ).catch(
            function(err){
              console.log(err)
            }
            
          )
          this.goods = list;
        }
       
          console.log(list)
      },
      onSearch(val) {
        Toast(val);
      },
      onCancel() {
        Toast('取消');
      },
      changeAppLocale(locale) {
        this.changeLanguage(locale);
      },
      hreftwo(url,type) {
        if(type){
          this.$router.push({ path: url + "/?" + type  });
        }else{
          this.$router.push({ path: url });
        }
      
    },
      onSave() {
        Toast('save');
      },
      onDelete() {
        Toast('delete');
      },
    },
    mounted() {
      var _this=this
      this.$axios.get('https://timenftbsc.github.io/goods.json').then(
      function(res){
        _this.goods = res.data;
        console.log( _this.goods)
      }
    ).catch(
      function(err){
        console.log(err)
      }
      
    )
    },
    destroyed() {
  
    },
  };
  </script>
    
    <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped  lang="less">
  .details{
    height: 100%;
    .title{
      font-size: 16px;
      text-align: center;
      font-weight: normal;
      padding: 15px;
      background: #fff;
      margin-bottom:1px;
    }
  }
  .content {
    margin-top: 2px;
    height: 100%;
    ul{
      height: 100%;
      li{
        text-align: center;
        font-size: 14px;
        color: #999;
        padding: 15px 0;
      }
      li.in{
        color: #ff1773;
        font-weight: bold;
        background: #fff;
        border-left: 2px solid #ff1773;
      }
    }
    .box{
      padding: 20px;
      background: #fff;
      min-height: 90%;
      text-align: center;
      .box_1{
        margin:20px 0 30px 0;
        text-align: center;
      }
    }
  }
  </style>
    


