<template>
  <div class="home">
    <div class="head">
      <van-row gutter="20" type="flex" justify="space-between">
          <van-col span="8">
            <img src="../../assets/img/fan/logo.png" alt="" width="80" class="logo">
          </van-col>
          <van-col span="16">
            <div class="qianbao">
              <img src="../../assets/img/fan/no.png" alt="" width="30">
              <p>{{protectAddress(account,5)}}</p>
            </div>
          </van-col>
        </van-row>
    </div>
    <div class="content">
      <div class="swipe">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
              <van-swipe-item><a href="http://flaremetas.com"><img :src="require('../../assets/img/fan/banner_1.png')" alt="" width="100%"></a></van-swipe-item>
              <van-swipe-item><img :src="require('../../assets/img/fan/banner_2.png')" alt="" width="100%"></van-swipe-item>
                    <van-swipe-item><a href="https://timenftbsc.github.io/#/"><img :src="require('../../assets/img/fan/banner_3.png')" alt="" width="100%">></a></van-swipe-item>
                    <van-swipe-item><img :src="require('../../assets/img/fan/banner_4.png')" alt="" width="100%"></van-swipe-item>
          </van-swipe>
      </div>
      <div class="price">
        <van-row gutter="20" type="flex" justify="space-between">
          <van-col span="12">
            当前Future币价
          </van-col>
          <van-col span="12" style="text-align: right;color:#FF1073; font-weight: bold;font-size: 18px;">
            ${{userInfo ? userInfo.AESPrice : 0.00}}
          </van-col>
        </van-row>
      </div>
      <div class="fen">
        <van-row gutter="20" type="flex" justify="space-between">
          <van-col span="6" @click="hreftwo('/invitation')">
            <img src="../../assets/img/fan/icon1.png" alt="" width="50" class="logo">
            <span>邀 请</span>
          </van-col>
          <van-col span="6" @click="hreftwo('/order')">
            <img src="../../assets/img/fan/icon2.png" alt="" width="50" class="logo">
            <span>订 单</span>
          </van-col>
          <van-col span="6" @click="hreftwo('/team')">
            <img src="../../assets/img/fan/icon3.png" alt="" width="50" class="logo">
            <span>团 队</span>
          </van-col>
          <van-col span="6" @click="hreftwo('/bill')">
            <img src="../../assets/img/fan/icon4.png" alt="" width="50" class="logo">
            <span>补 贴</span>
          </van-col>
        </van-row>
      </div>

      <div class="notice" v-if="list" @click="hreftwo('/notice')">
        <img src="../../assets/img/fan/notice.png" alt="" width="30">
        <van-notice-bar color="#333" background="#fff" left-icon="info-o">{{ list.phrase}} </van-notice-bar>
      </div>

      <h1 class="title">为你推荐</h1>
      <div class="list">
          <van-row gutter="10" type="flex" justify="space-between">
            <van-col span="12" v-for="(itme,index) in goods" :key="index">
              <div class="box" @click="hreftwo('/details','id='+itme.id)" >
                  <div class="bg">
                    <img :src="itme.insets" alt="" width="" >
                  </div>
                  <h2>{{ itme.name }}</h2>
                  <h3>$ {{ itme.price }} <van-icon name="add" /></h3>
              </div>
            </van-col>
          </van-row>
      </div>
    </div>
    
    <van-tabbar v-model="active" active-color="#000" inactive-color="#565656">
        <van-tabbar-item @click="hreftwo('/home')"> 
          <span>首 页</span>
          <template slot="icon" slot-scope="props">
            <img :src="props.active ? icon.active : icon.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="hreftwo('/classify')">
          <span>分 类</span>
          <template slot="icon">
           <img src="../../assets/img/fan/lei1.png" alt="">
          </template>
        </van-tabbar-item>
        <van-tabbar-item @click="hreftwo('/user')">
          <span>个人中心</span>
          <template slot="icon">
            <img src="../../assets/img/fan/user1.png" alt="">
          </template>
        </van-tabbar-item>
      </van-tabbar>
    <!-- <van-tabbar v-model="active" active-color="#f2b44f" inactive-color="#000">
      <van-tabbar-item icon="home-o" @click="hreftwo('/home')">首页</van-tabbar-item>
      <van-tabbar-item icon="apps-o" @click="hreftwo('/classify')">分类</van-tabbar-item>
      <van-tabbar-item icon="friends-o" @click="hreftwo('/user')">个人中心</van-tabbar-item>
    </van-tabbar>  -->
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import data from "../../assets/data.json";
import notice from "../../assets/notice.json";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "en",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      isFiveApprove: false,
      timer: null,
      timer2: null,
      nbn: 0,
      tab: 1,
      active:0,
      icon: {
        active: 'https://timebabycat.github.io/home.png',
        inactive: 'https://timebabycat.github.io/home1.png',
      },
      list:{},
      goods:[]
    };
  },
  computed: {
    ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "userInfo"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      "add_usdtCoinContract",
      "add_buyContract"
    ]),
    hreftwo(url,type) {
        if(type){
          this.$router.push({ path: url + "/?" + type  });
        }else{
          this.$router.push({ path: url });
        }
      
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(37, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },

  },
  created() {
    // this.add_mxtCoinContract();
    // this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHs");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }
    // data.forEach()
    // data.forEach(element => {
      
    // });
    this.$axios.get('https://timenftbsc.github.io/goods.json').then(
      function(res){
        _this.goods = res.data;
        console.log(_this.goods )
      }
    ).catch(
      function(err){
        console.log(err)
      }
      
    )
    this.add_usdtCoinContract();

    this.$axios.get('https://timenftbsc.github.io/notice.json').then(
        function(res){
          console.log(res.data[0])
          _this.list = res.data[0];
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )

          var getData = () => {
      this.add_buyContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.buyContract || !this.userInfo) {
          getData();
        } else {

          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);

  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.home{
  padding-bottom: 60px;
  background: url(../../assets/img/fan/home_bg.png) top center no-repeat #FCEEF3;
    background-size: 100%;
}
.content{
  width:94%;
  margin: 0 auto;
  .title{
    font-size: 16px;
    margin: 20px 0 15px;
font-family: PingFang SC;
  }
  .box{
    background: url(../../assets/img/fan/list_bg.png) top center no-repeat #FCEEF3;
    background-size: 100% 100%;
    border-radius: 10px;
    padding-bottom: 10px;
    margin-bottom: 15px;
    .bg{
      // background: #d5d5d5;
      border-radius: 10px 10px 0 0;
      // padding: 5px 0 0;
      margin: 0 auto;
      // border: 2px solid #d5d5d5;
      img{
      border-radius: 10px;
      width: 100%;
      display: block;
      margin: 0 auto;
    }
    }
    h2{
      font-size: 14px;
      margin: 10px 10px 5px;
      font-weight: normal;
      line-height: 20px!important;
      height: 40px;
			display: -webkit-box;
			text-overflow: ellipsis;
			overflow: hidden;
			-webkit-box-orient:vertical;
			-webkit-line-clamp:2;
    }
    h3{
      font-size: 18px;
      margin: 0 10px;
      color: #FF1073;
      .van-icon{
        float: right;
      }
    }
  }
}
.head{
  margin: 0 15px;
  padding: 15px 0;
.qianbao{
  width: 70%;
  background: #FFD91A;
  padding: 5px 0;
  float: right;
  justify-content: center;
  border-radius: 20px;
  display: flex;
  align-items: center;
  img{margin-right: 5px;}
}
}
.fen{
  padding:20px 0 15px;
  text-align: center;
  img{margin: 0 auto;display: block;}
span{
  display: block;
  text-align: center;
  margin-top: 5px;
}
}
.notice{
  // background: #fff;
  border:1px solid #FF1073;
  border-radius: 30px;
  position: relative;
  margin: 5px 0;
  img{
    top:7px;
    left:8px;
    position: absolute;
    z-index: 2;
  }
}
.price{
  border:1px solid #FF1073;
  border-radius: 30px;
  background: #fff;
  padding: 10px 20px;
  color: #000;
}
</style>



