<template>
  <div class="details">
    <van-nav-bar title="订单详情" left-arrow @click-left="onClickLeft" />
    <div class="content">
      <div class="tip" v-if="logistics.express">
        <h2>卖家已发货</h2>
        <p>请尽快查收~</p>
      </div>
      <div class="tip" v-else>
        <h2>卖家未发货</h2>
        <p>请耐心等待~</p>
      </div>
      <div class="addr" v-if="AddressInfo">
        <van-row type="flex" justify="space-between" align="center">
          <van-col span="24">
            <van-row gutter="20" type="flex" justify="space-between">
              <van-col span="6">
                <h4>收货人</h4>
              </van-col>
              <van-col span="18">
                <div class="box">
                  <h3>{{ AddressInfo.name }}</h3>
                  <h3>{{ AddressInfo.tel }}</h3>
                </div>
              </van-col>
            </van-row>
            <van-row gutter="20" type="flex" justify="space-between">
              <van-col span="6">
                <h4>收货地址</h4>
              </van-col>
              <van-col span="18">
                <div class="box">
                  <h3>{{ AddressInfo.province }}{{ AddressInfo.county }}{{ AddressInfo.city }}{{ AddressInfo.addressDetail }}
                  </h3>
                </div>
              </van-col>
            </van-row>
          </van-col>
        </van-row>

      </div>

      <div class="goods">
        <h2 class="title">商品信息</h2>
        <div v-if="list.times">
          <div class="card">
            <van-row gutter="20" type="flex" justify="space-between">
              <van-col span="8"><img :src="list.times.insets" alt="" width=""></van-col>
              <van-col span="16">
                <div class="box">
                  <h2>{{ list.times.name }}</h2>
                  <h3>{{ list.times.phrase }}</h3>
                  <h1>${{ list.times.price }} <span>x{{ Number(Number(list.investAmount) / 0.6) / Number(list.times.price) |
                      price4
                  }}</span></h1>
                </div>
              </van-col>
            </van-row>
            <div class="info">
              <p>合计：<span style="color: #ff4a6f;font-size: 18px;">${{ Number(list.investAmount) / 0.6 | figure }}</span></p>
              <p>支付方式：线上支付</p>
              <p>订单编号：{{ 100000000 + Number(list.orderId) }}</p>
            </div>
          </div>
        </div>
      </div>

    </div>

    <div class="wuliu" v-if="logistics.express">
      <h2 class="title">物流信息</h2>
      <van-steps direction="vertical" :active="0" active-color="">
        <van-step>
          <h3>已发货</h3>
          <h2 style="padding:20px 0 10px;color:#333">快递公司：{{ logistics.expressCompany }}</h2>
          <p v-clipboard:copy="logistics.express" v-clipboard:success="onCopy" v-clipboard:error="onError" style="color:#333">
            快递单号：{{ logistics.express }}
            <van-icon name="add-square" color="#ff1773" size="18" style="vertical-align: bottom;" />
          </p>
        </van-step>
      </van-steps>
    </div>

  </div>
</template>
  
<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import { areaList } from '@vant/area-data';
import data from "../../assets/data.json";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      areaList,
      goods: {},
      list: {},
      AddressInfo: {},
      logistics: {}
    };
  },
  computed: {
    ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "userInfo",
      "listData",
      "getListContract"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      "add_usdtCoinContract",
      "add_buyContract",
      "add_getListContract"
    ]),
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    onClickLeft() {
      this.hreftwo('/user');
    },
    hreftwo(url) {
      this.$router.push({ path: url });
    },
    onSave() {
      Toast('save');
    },
    onDelete() {
      Toast('delete');
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: '黄龙万科中心',
            address: '杭州市西湖区',
          },
        ];
      } else {
        this.searchResult = [];
      }
    },
    getList() {
      if (this.userInfo == null) {
        return;
      }
      _this.list = {};
      var investsLength = _this.userInfo.info[13];
      var times = parseInt(investsLength) - 1;
      var account = _this.account;

      for (let i = times; i > -1; i--) {

        this.buyContract.methods
          .getInvestInfo(
            account,
            i
          )
          .call()
          .then((res) => {
            //prop tips
            var investAmount = res[1];	//投资的金额
            var limitAmount = res[2];	//预计收益
            var earnAmount = res[3];	//当前收益
            var investTime = res[4];
            var time = res[5];	//投资时间
            var orderId = res[6];	//订单id
            var goodId = res[7];	//商品id
            var addrId = res[9];	//地址下标
            console.log(res)
            if (i == 0) {
              earnAmount = Number(earnAmount) - Number(_this.userInfo.info[3])
            }
            console.log(goodId)
            if (investAmount > 0) {

              _this.$axios.get('https://timenftbsc.github.io/goods.json').then(
                function (res) {
                  var good = res.data;
                  good.forEach(element => {
                    if (element.id == goodId) {
                      // _this.goods =element;
                      if (Number(investAmount) * 2 != Number(earnAmount)) {
                        if (element.id == _this.$route.query.id) {

                          // 获取地址
                          var lists = _this.listData[addrId].split(",");
                          _this.AddressInfo = {
                            addressDetail: lists[4],
                            areaCode: lists[0],
                            city: lists[3],
                            county: lists[2],
                            name: lists[5],
                            province: lists[1],
                            tel: lists[6],
                            id: _this.listData[addrId],
                            isDefault: lists[7] == 1 ? true : false,
                          }

                          if (_this.logistics.length > 0) {
                            _this.logistics.forEach(itme => {
                              if (itme.orderNumber == orderId) {

                                _this.list = {
                                  id: i,
                                  investAmount: investAmount,
                                  earnAmount: earnAmount,
                                  limitAmount: limitAmount,
                                  goodId: goodId,
                                  investTime: investTime,
                                  times: element,
                                  orderId: orderId,
                                  express: itme.express
                                }

                              } else {
                                _this.list = {
                                  id: i,
                                  investAmount: investAmount,
                                  earnAmount: earnAmount,
                                  limitAmount: limitAmount,
                                  goodId: goodId,
                                  investTime: investTime,
                                  times: element,
                                  orderId: orderId,
                                }
                              }

                            })
                          } else {
                            _this.list = {
                              id: i,
                              investAmount: investAmount,
                              earnAmount: earnAmount,
                              limitAmount: limitAmount,
                              goodId: goodId,
                              investTime: investTime,
                              times: element,
                              orderId: orderId,
                            }
                          }
                        }

                      }
                    }
                  });
                  console.log(_this.list)
                }
              ).catch(
                function (err) {
                  console.log(err)
                }
              )

            } else {
              _this.list = {};
            }


          })
          .catch((err) => {
            this.$toast.clear();
            this.$toast(err.message);
            console.log(err);
          });


      }
      console.log(_this.list)
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");

    // this.$axios.get('https://timenftbsc.github.io/goods.json').then(
    //         function(res){
    //           var good= res.data;
    //           good.forEach(element => {
    //         if(element.id==_this.$route.query.id){
    //           _this.goods =element;
    //           console.log(_this.goods)
    //         }
    //     });
    //         }
    //       ).catch(
    //         function(err){
    //           console.log(err)
    //         }
    //       )

    var getData = () => {
      this.add_buyContract();
      this.add_usdtCoinContract();
      this.add_getListContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!_this.buyContract || !_this.usdtCoinContract || !_this.getListContract || !_this.listData) {
          getData();
        } else {
          // _this.getList();

          if (this.userInfo == null) {
            return;
          }
          _this.list = {};
          var investsLength = _this.userInfo.info[13];
          var times = parseInt(investsLength) - 1;
          var account = _this.account;

          for (let i = times; i > -1; i--) {

            this.buyContract.methods
              .getInvestInfo(
                account,
                i
              )
              .call()
              .then((res) => {
                //prop tips
                var investAmount = res[1];	//投资的金额
                var limitAmount = res[2];	//预计收益
                var earnAmount = res[3];	//当前收益
                var investTime = res[4];
                var time = res[5];	//投资时间
                var orderId = res[6];	//订单id
                var goodId = res[7];	//商品id
                var addrId = res[9];	//地址下标
                console.log(res)
                if (i == 0) {
                  earnAmount = Number(earnAmount) - Number(_this.userInfo.info[3])
                }
                console.log(goodId)
                if (investAmount > 0) {

                  _this.$axios.get('https://timenftbsc.github.io/goods.json').then(
                    function (res) {
                      var good = res.data;
                      good.forEach(element => {
                        if (element.id == goodId) {
                          // _this.goods =element;
                          // if(Number(investAmount)*2 != Number(earnAmount)){
                          if (element.id == _this.$route.query.id) {

                            // 获取地址
                            var lists = _this.listData[addrId].split(",");
                            _this.AddressInfo = {
                              addressDetail: lists[4],
                              areaCode: lists[0],
                              city: lists[3],
                              county: lists[2],
                              name: lists[5],
                              province: lists[1],
                              tel: lists[6],
                              id: _this.listData[addrId],
                              isDefault: lists[7] == 1 ? true : false,
                            }


                            // 获取是否发货

                            _this.$axios.get('https://timenftbsc.github.io/user/' + _this.account + '.json').then(
                              function (obj) {
                                _this.logistics = obj.data;
                                _this.logistics.forEach(arr => {
                                  console.log(arr.orderNumber == Number(orderId) + 100000000)
                                  if (arr.orderNumber == Number(orderId) + 100000000) {
                                    _this.list = {
                                      id: i,
                                      investAmount: investAmount,
                                      earnAmount: earnAmount,
                                      limitAmount: limitAmount,
                                      goodId: goodId,
                                      investTime: investTime,
                                      times: element,
                                      orderId: orderId
                                    }
                                    _this.logistics =arr;
                                    console.log(arr,_this.list)
                                  }
                                });
                                console.log(_this.logistics)
                              }
                            ).catch(
                              function (err) {
                                console.log(err)
                              }
                            )

                            // console.log(element.orderNumber , Number(orderId) + 100000000)
                            // // 获取是否发货
                            // if (element.orderNumber == Number(orderId) + 100000000) {

                            //   _this.list = {
                            //     id: i,
                            //     investAmount: investAmount,
                            //     earnAmount: earnAmount,
                            //     limitAmount: limitAmount,
                            //     goodId: goodId,
                            //     investTime: investTime,
                            //     times: element,
                            //     orderId: orderId
                            //   }

                            // }
                          }
                        }
                      });
                      console.log(_this.list)
                    }
                  ).catch(
                    function (err) {
                      console.log(err)
                    }
                  )

                } else {
                  _this.list = {};
                }


              })
              .catch((err) => {
                this.$toast.clear();
                this.$toast(err.message);
                console.log(err);
              });


          }
          console.log(_this.list)



          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    // this.timer2 = setInterval(() => {
    //   if (this.timer) {
    //     clearInterval(this.timer);
    //     this.timer = null;
    //   }
    //   getDataCalculate();
    // }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.content {
  // margin-top: 50px;
  background: #fff;
  padding: 20px 0 0;

  .tip {
    background: url('../../assets/img/goods/order_bg.png') no-repeat #fff;
    background-size: 100% 100%;
    margin: 0 20px 20px;
    color: #fff;
    padding: 25px 20px;

    h2 {
      margin-bottom: 10px;
    }
  }

  .addr {
    // margin:0 20px;
    background: url('../../assets/img/goods/line.png') bottom no-repeat #fff;
    background-size: 100%;
    padding: 10px 20px 20px 20px;

    h3 {
      font-size: 14px;
      color: #000;
      font-weight: normal;
      margin-top: 10px;
    }

    h4 {
      font-size: 14px;
      color: #999999;
      font-weight: normal;
      margin-top: 10px;
    }
  }

  .goods {
    margin-top: 30px;

    .title {
      margin: 0 20px;
    }
  }
}

.card {
  background: #fff;
  padding: 10px;
  margin-top: 10px;

  img {
    width: 100%;
    border-radius: 5px;
  }

  .box {

    h2 {
      font-size: 16px;
      font-weight: normal;
      color: #333333;
      padding-top: 10px;
    }

    h3 {
      font-size: 14px;
      font-weight: normal;
      color: #999;
      padding-top: 5px;
    }

    h1 {
      font-size: 18px;
      font-weight: normal;
      color: #ff4a6f;
      padding-top: 20px;

      span {
        float: right;
        font-size: 14px;
        color: #999;
      }
    }
  }

  .info {
    border-top: 1px solid #f5f5f5;
    padding: 20px 0 10px;
    margin-top: 5px;

    p {
      font-size: 15px;
      color: #333;
      text-align: right;
      margin-top: 5px;
    }
  }
}

.title {
  color: #000;
  font-size: 15px;
  border-bottom: 1px solid #f0f0f0;
  padding-bottom: 10px;
}

.wuliu {
  background: #fff;
  margin-top: 10px;
  padding: 20px;

  .title {
    margin-bottom: 10px;
  }
}
</style>
  


