<template>
    <div class="details">
      <van-nav-bar
  title="商品详情"
  left-arrow
  @click-left="onClickLeft"
/>
      <div class="content">
        <div class="swipe">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="gray" v-if="goods">
                <van-swipe-item  v-for="(itme,index) in goods.banner" :key="index"><img :src="itme" alt="" width="100%"></van-swipe-item>
                <!-- <van-swipe-item><img :src="require('../../assets/img/goods/2.jpg')" alt="" width="100%"></van-swipe-item>
                <van-swipe-item><img :src="require('../../assets/img/goods/3.jpg')" alt="" width="100%"></van-swipe-item> -->
                      <!-- <van-swipe-item><img src="../../assets/img/banner.png" alt="" width="100%"></van-swipe-item> -->
            </van-swipe>
        </div>
       <div class="goods" v-if="goods">
            <h3>$ {{goods?goods.price:'--'}}</h3>
            <h2>{{goods?goods.name:'--'}}</h2>
            <h4>{{goods?goods.phrase:'--'}}</h4>
            <p>运费 <span>免运费</span> </p>
            <p>保障 <span><van-icon name="passed" color="#fe5568"/>   商城自营</span> <span><van-icon name="passed" color="#fe5568"/>   售后无忧</span></p>
       </div>
       <div class="cont" v-if="goods">
            <h2>图文详情</h2>
            <!-- <divv-for="(itme,index) in goods.details" :key="index">

            </divv-for=> -->
            <img v-for="(itme,index) in goods.details" :key="index" :src="itme" alt="" width="100%" >
       </div>
      </div>
      
      <van-action-sheet v-model="show" title="选购商品" v-if="goods">
          <div class="content2">
            <div class="card">
              <van-row gutter="20" type="flex" justify="space-between" >
                <van-col span="8" >
                  
                  <img :src="goods.insets" alt="" width="" >
                </van-col>
                <van-col span="16">
                  <div class="box">
                    <h2>{{goods?goods.name:'--'}}</h2>
                    <h3>{{goods?goods.phrase:'--'}}</h3>
                    <h1>${{goods?goods.price:'--'}}</h1>
                  </div>
                </van-col>
              </van-row>
          </div>
          <van-row gutter="20" type="flex" justify="space-between" class="num">
            <van-col span="8"><span>购买数量</span></van-col>
            <van-col span="8">
              <van-stepper v-model="value" theme="round" button-size="22" disable-input />
            </van-col>
          </van-row>
          <van-submit-bar decimal-length="2" :price="goods.price*100*value" button-text="立即购买" button-color="#ff1773" @submit="hreftwo('/list','id='+goods.id+'&num='+value)" currency="$"/>
          </div>
        </van-action-sheet>

      <van-goods-action>
          <van-goods-action-icon icon="chat-o" text="客服"  @click="showTips"/>
          <van-goods-action-button
          color="#fe5568"
            type="danger"
            text="立即购买"
            @click="showSku"
          />
        </van-goods-action>


        <van-popup v-model="isFranslation" position="top" :style="{ height: 'auto', top: '10%',background:'transparent' }" :close-on-click-overlay="false">
            <div class="ts">
              <img src="../../assets/img/fan/tips.png" alt="" width="100%">
              <van-icon name="close" color="#fff" size="40px" @click="closTips"/>
            </div>
    </van-popup>
    </div>
  </template>
  
  <script type="es6">
  import HeadTop from "../../components/Header";
  import { mapActions, mapState } from "vuex";
  import data from "../../assets/data.json";
  import Web3 from "web3";
  export default {
    data() {
      return {
        lang: "",
        goods:[],
        banner:[],
        detailPics:[],
        tab: 1,
        show:false,
        value:1,
        isFranslation:false,
      };
    },
    computed: {
      ...mapState("main", []),
    },
    components: {
      HeadTop,
    },
    methods: {
      ...mapActions("main", []),
      onClickLeft(){
      console.log(this.$router)
      this.$router.go(-1)
    },
    closTips(){
      this.isFranslation=false
    },
    showTips(){
      this.isFranslation=true
    },
    showSku(){
      this.show=true
    },
    hreftwo(url,type) {
      console.log(type)
        if(type){
          this.$router.push({ path: url + "/?" + type  });
        }else{
          this.$router.push({ path: url });
        }
      
    },
      
      protectAddress: function (addr, num) {
        if (addr) {
          var newAddr = addr.replace('"', "").replace('"', "");
          if (newAddr == "0x0000000000000000000000000000000000000000") {
            return "--";
          }
          if (!newAddr) {
            return "--";
          }
          if (newAddr == 1) {
            return newAddr;
          }
          return (newAddr + "").length == 42
            ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
            : newAddr;
        }
      },
      changeLanguage(lang) {
        this.$i18n.locale = lang;
        this.lang = lang;
      },
      changeAppLocale(locale) {
        this.changeLanguage(locale);
  
        // this.$router.push({
        //   name: this.$route.name,
        //   params: { lang: this.lang },
        //   query: { r: localStorage.getItem("r") },
        // });
      },
    },
    created() {
    },
    mounted() {
      var _this = this;
      this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
// console.log(_this.$route.query.id)
      // data.forEach(element => {
      //       if(element.id==_this.$route.query.id){
      //         _this.goods =element
      //       }
      //   });
        this.$axios.get('https://timenftbsc.github.io/goods.json').then(
            function(res){
              var good= res.data;
              good.forEach(element => {
            if(element.id==_this.$route.query.id){
              _this.goods =element
            }
        });
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )

    },
    destroyed() {
    },
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped  lang="less">
    .goods{
      background: #fff;
    //   padding-bottom: 20px;
      padding-top: 15px;
      margin-bottom: 8px;
      h2{
        font-size: 15px;
        color: #333;
        font-weight: normal;
        margin: 5px 10px 2px;
              display: -webkit-box;
              text-overflow: ellipsis;
              overflow: hidden;
              -webkit-box-orient:vertical;
              -webkit-line-clamp:2;
      }
      h4{
        font-size: 14px;
        color: #999999;
        font-weight: normal;
        margin: 0 10px 12px;
      }
      h3{
        font-size: 24px;
        margin: 0 10px;
        color: #ff4a6f;
      }
      p{
        border-top: 2px solid #f5f5f5;
        padding:15px;
        color: #999;
        span{
            color: #000;
            margin-left: 20px;
            img{
                width: 13px;
            }
        }
      }
    }
    .cont{
        margin-top: 5px;
        padding-bottom: 50px;
        background: #fff;
        h2{margin: 0 10px 0;font-size: 14px;padding: 18px 0;font-weight: normal;text-align: center;}
    }
    .foot{
        position: fixed;
        bottom:0;
        background: #fff;
        padding: 20px 0;
        width: 100%;
        border-top: 2px solid #f5f5f5;
    }
    .btn{
        background: #f2b44f;
        width: 80%;
        margin: 0 auto;
        display: block;
        border: none;
        text-align: center;
        font-size: 18px;
        padding: 10px 0;
        border-radius: 30px;
        font-weight: bold;
    }
    .card{ 
  background: #fff;
  padding: 10px;
  img{width: 100%;border-radius: 5px;}
  .box{
   
    h2{
      font-size: 16px;
      font-weight: normal;
      color:#333333;
      padding-top: 10px;
    }
    h3{
      font-size: 14px;
      font-weight: normal;
      color:#999;
      padding-top: 5px;
    }
    h1{
      font-size: 18px;
      font-weight: normal;
      color:#ff4a6f;
      padding-top: 20px;
      span{
        float: right;
        font-size: 14px;
        color:#999;
      }
    }
  }
  .info{
    border-top: 1px solid #f5f5f5;
    padding: 20px 0 10px; 
    margin-top: 5px;
    p{
    font-size: 15px;
    color:#333;
    text-align: right;
    margin-top: 5px;
  }
  } 
}
.content2{
  padding-bottom: 50px;
  .num{
    border-top: 1px solid #f5f5f5;
    margin: 10px 0px 10px 20px;
    padding: 20px 0;
  }
}
.ts{
  width: 60%;
  margin: 0 auto;
  text-align: center;
}
  </style>
  


