<template>
  <header>
    <div id="header">
      <van-row align="center" justify="space-between" type="flex">
        <van-col span="6">
          <a v-if="isHome" @click="hreftwo('/home')">
            <img
              style="width:35px;"
              src="../assets/img/fan/logo.png"
            /> </a
        >
          <a v-else @click="hreftwo('/home')">
            <img
              style="width:10px; margin-top: 5px"
              src="../assets/img/fan/bank.png"
            /> </a
        >
        </van-col>
        <van-col span="12" >
              <div  class="name">{{name}}</div>
        </van-col>
        <van-col span="6"  @click="showPopup" class="textRight"    style="margin-top: 5px;"><div style="width:25px">sss</div></van-col>
      </van-row>

      <van-popup
        v-model="show"
        position="right"
        :style="{ height: '100%', width: '65%' ,background: '#fff'}"
      >
        <div class="menu">  
           <div class="bgWhite flex" >
                <div class="bg_logo">
                  <img src="../assets/img/fan/logo.png" alt="" />
                  <span>BSC</span>
                </div>
                <p>{{ protectAddress(this.account, 4) }}</p>
              </div>
              <div class="list">
                 <div class="li_col">
                    <a href="">首 页</a>  
                  </div>
                  <div class="li_col">
                    <a href="https://bscfuture.github.io/#/">Meta Future</a>
                  </div>
                  <div class="li_col">
                    <a href="https://timenftbsc.github.io/#/">NFT</a>
                  </div>
                  <div class="li_col">
                    <a @click="hreftwo('/order')">订单列表</a>
                  </div>
                </div>

        </div>
      </van-popup>
    </div>
  </header>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show: false,
      lang_1:null,
      langList: [
        {
          text: "中文",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
    type: {
      type: String,
      default: "home",
    },
     name: {
      type: String,
      default: "",
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "oktInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_oktInfo"]),
    showPopup() {
      this.show = true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      // this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    tips() {
      this.$toast.fail(this.$t("message.tips3"));
      // this.$dialog.alert({
      //   message: this.$t("message.tips3"),
      //   theme: "round-button",
      // });
    },
    prev() {
      this.$router.go(-1);
    },

    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url) {
      // console.log(url.length);
      // if (url.length > 1) {
      //   this.$router.push({ path: url + "/" + this.lang });
      // } else {
        if(url.length>2){
          this.$router.push({ path: url + "/?invite=" + this.$route.query.invite  });
        }else{
          this.$router.push({ path: url });
        }
      
      // }
    },
  },
};
</script>

<style scoped lang="less">
#header {
  padding: 15px 10px 10px;
  position: fixed;
  background: #fff;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  z-index:10;
}
  .name{
    color: #000;
    font-size: 17px;
    text-align: center;
  }
.bgWhite {
  padding: 9px 5px;
  background: url(../assets/img/fan/bsc_line.png) top center no-repeat;
    background-size: 100%;
  // border-radius: 30px;
  opacity: 0.8;
  p {
    margin-left: 10px;
    height: 25px;
    line-height: 25px;
    color: #fff;
  }
}
.flex {
  display: flex;
}
.bg_logo {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 600;
  color: #fff;
  background:rgb(0 0 0 / 60%);
  border-radius: 30px;
  // border: 1px solid #e1e1e1;
  padding: 0 6px 0 0;
  span{display:inline-block;vertical-align: text-bottom;}
  img {
    width: 25px;
    vertical-align: sub;
  }
}
.menu {
  padding: 30px 20px;
  position: relative;
  height: 90%;
  text-align: center;
  .list {
    // border-top: 2px solid #182034;
    margin-top: 10px;
    padding-top: 10px;
    img {
      vertical-align: bottom;
      float: left;
      margin-top: 15px;
    }
    a {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 500;
      color: #000;
      display: block;
      padding: 12px 20px 12px 30px
    }
  }
   .li_col li.in{color:#4DA9DB;}
  .li_col li{
    padding: 5px 26px;
    color: #999;
    .gou{
          width: 20px;
    float: none;
    margin-top: 0;
    margin-left: 20px;
    }
  }

  .foot {
    position: absolute;
    bottom: 20px;
    img {
      display: inline-block;
      margin-right: 8px;
    }
  }
}
</style>



