<template>
    <div class="order">
      <van-nav-bar
    :border=false
    title="系统公告"
    left-arrow
    @click-left="onClickLeft"
  />
     <!-- <div class="my">
      <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="24">
          <h1>4545,24545</h1>
          <span>总消费（USDT）</span>
        </van-col>
      </van-row>
      <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="12">
          <h3>455,24545</h3>
          <span>累计补贴（USDT）</span>
        </van-col>
        <van-col span="12">
          <h3>4545,24545</h3>
          <span>待释放(USDT)</span>
        </van-col>
      </van-row>
     </div> -->

     <div class="list" v-if="list">
      <div class="box" v-for="(itme,index) in list" :key="index">
        <h3>公告</h3>
        <div class="cont">
            <h4>{{itme.name}}</h4>
            <span>{{itme.time}}</span>
            <p>{{itme.phrase}}</p>
        </div>
      </div>

     </div>
    </div>
  </template>
    
  <script type="es6">
  import HeadTop from "../../components/Header";
  import { mapActions, mapState } from "vuex";
  import { areaList } from '@vant/area-data';
  import notice from "../../assets/notice.json";
  export default {
    data() {
      return {
        lang: "en",
        currency: "",
        link: "--",
        searchResult: [],
        areaList,
        list:[]
      };
    },
    computed: {
      ...mapState("main", []),
    },
    components: {
      HeadTop,
    },
    methods: {
      ...mapActions("main", []),
      changeAppLocale(locale) {
        this.changeLanguage(locale);
      },
      onClickLeft(){
        console.log(this.$router)
        this.$router.go(-1)
      },
      hreftwo(url) {
        if (url.length > 2) {
          this.$router.push({ path: url + "/?invite=" + this.$route.query.invite });
        } else {
          this.$router.push({ path: url });
        }
      },

  
    },
    mounted() {
      var _this = this;
      this.$axios.get('https://timenftbsc.github.io/notice.json').then(
        function(res){
          _this.list = res.data;
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )
      },
    destroyed() {
  
    },
  };
  </script>
    
    <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped  lang="less">
  .order{
    background: url('../../assets/img/fan/notice_bg.png') top no-repeat#fff ;
    background-size: 100%;
    min-height: 100%;
    padding-bottom: 20px;
    .van-nav-bar{
        background: transparent !important;
        color: #fff;
    }
  }

.list{
  width: 92%;
  margin: 30px auto 0;
  .box{
    background: #fff;
    padding: 20px ;
box-sizing: border-box;
border-radius: 10px; box-shadow: 0 1px 10px #ddd;
    display: flex;
    justify-content: space-between;
    grid: 20px;
   margin-top: 20px;
    h3{
      margin-bottom: 5px;
      color: #333;
      font-size: 15px;
      width: 15%;
    }
    h4{
      color: #333;
      font-size: 15px;
      font-weight: normal;
      margin: 0 0 5px;
    }
    span{color:#999}
    .cont{
        width: 85%;
    }
    p{
        margin-top: 10px;
      color: #666;
      font-size: 14px;
      letter-spacing: 1px;
      line-height: 1.5;
      
    }
  }
}
.textR{text-align: right;}
  </style>
    
  
  


