<template>
    <div class="order">
      <img src="../../assets/img/fan/team_bg.png" alt="" width="50%" class="bg">
      <van-nav-bar
        title="我的团队"
        left-arrow
        :border=false
        @click-left="onClickLeft"
      />
     
      <div class="user_info">
          <img src="../../assets/img/fan/face.png" alt="" width="70">
            <div class="right">
              <h2>{{ protectAddress(account, 8) }}</h2>
                  <span class="level"><img src="../../assets/img/fan/team_1.png" alt="" width="23"> 加速补贴:{{ userInfo ? Number(Number(userInfo.info[8])/100)  : "0"}}%</span>
            </div>
                
        </div>

        <div class="cont">
          <van-row gutter="20" type="flex" justify="space-between" align="center">
            <van-col span="12">
              <span>我的级别</span>
            <h2>{{ userInfo ?
              levelFun(userInfo.info[7]) : '--'
          }}</h2>
            </van-col>
            <van-col span="12">
              <button @click="hreftwo('/invitation')">立 即 邀 请</button>
              </van-col>
          </van-row>
        </div>

        <div class="team">
          <h3>团队详情</h3>
          <van-row gutter="10" type="flex" justify="space-between">
            <van-col span="12">
              <div class="box">
                <span>团队人数</span>
                <h1>{{ userInfo ? (Number(userInfo.info[20]))  : "0" }}</h1>
                <img src="../../assets/img/fan/t_1.png" alt="" width="100%">
                
              </div>
            </van-col>
            <van-col span="12">
              <div class="box">
                <span>直推人数</span>
                <h1>{{ userInfo ? (Number(userInfo.info[11]))  : "0" }}</h1>
                <img src="../../assets/img/fan/t_2.png" alt="" width="100%">

              </div>
            </van-col>
            <van-col span="12">
              <div class="box">
                <span>大区业绩(U)</span>
                <h1>{{ userInfo ? (Number(userInfo.info[19]))  : "0" | figure0 }}</h1>
                <img src="../../assets/img/fan/t_5.png" alt="" width="100%">
                
              </div>
            </van-col>
            <van-col span="12">
              <div class="box">
                <span>小区业绩(U)</span>
                <h1 v-if="userInfo&&(userInfo.info[19]<=0) ">0</h1>
                <h1 v-else>{{ userInfo ? (Number(userInfo.info[23])-Number(userInfo.info[19]))  : "0" | figure0 }}</h1>
                <img src="../../assets/img/fan/t_6.png" alt="" width="100%">
              
              </div>
            </van-col>
            <van-col span="12">
              <div class="box">
                <span>团队业绩(U)</span>
                <h1>{{ userInfo ? (Number(userInfo.info[23]))  : "0" | figure0 }}</h1>
                <img src="../../assets/img/fan/t_3.png" alt="" width="100%">
                
              </div>
            </van-col>
            <van-col span="12">
              <div class="box">
                <span>直推业绩(U)</span>
                <h1>{{ userInfo ? (Number(userInfo.info[21]))  : "0" | figure0 }}</h1>
                <img src="../../assets/img/fan/t_4.png" alt="" width="100%">
              
              </div>
            </van-col>
            </van-row>
        </div>
        
        <ul class="list">
          <li>我的上级<span>{{userInfo ? protectAddress(userInfo.referrer, 8) : '--'}}</span></li>
          <li>推荐专员<span>{{userInfo ? userInfo.info[26] : '--'}} 人</span></li>
          <li>推荐主管<span>{{userInfo ? userInfo.info[27] : '--'}} 人</span></li>
          <li>推荐经理<span>{{userInfo ? userInfo.info[28] : '--'}} 人</span></li>
          <li>推荐总监<span>{{userInfo ? userInfo.info[29] : '--'}} 人</span></li>
          <li>推荐总裁<span>{{userInfo ? userInfo.info[30] : '--'}} 人</span></li>

        </ul>
    </div>
  </template>
    
  <script type="es6">
  import HeadTop from "../../components/Header";
  import { mapActions, mapState } from "vuex";
  import { areaList } from '@vant/area-data';
  export default {
    data() {
      return {
        lang: "en",
        currency: "",
        link: "--",
          timer:null,
        timer2:null,
      };
    },
    computed: {
      ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "userInfo"
      ]),
    },
    components: {
      HeadTop,
    },
    methods: {
      ...mapActions("main", [
      "add_usdtCoinContract",
      "add_buyContract"
      ]),
      protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(38, 42)
          : newAddr;
      }
    },
      changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
      changeAppLocale(locale) {
        this.changeLanguage(locale);
      },
      onClickLeft(){
        console.log(this.$router)
        this.$router.go(-1)
      },
      levelFun(type) {
        switch (type) {
          case '1':
            return '专员';
          case "2":
            return '主管'
            break;
          case "3":
            return '经理'
            break;
          case "4":
            return '总监'
            break;
          case "5":
            return '总裁'
            break;
          default:
            return '普通会员'
        }
    },
      hreftwo(url) {
        if (url.length > 2) {
          this.$router.push({ path: url + "/?invite=" + this.$route.query.invite });
        } else {
          this.$router.push({ path: url });
        }
      },

  
    },
    mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_buyContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.buyContract || !this.usdtCoinContract) {
          getData();
        } else {

          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  };
  </script>
    
    <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped  lang="less">
  .order{
    // background:linear-gradient(#FCEEF3,#fff);
    background: #FCEEF3;
    // background-size: 100%;
    min-height: 100%;
    position: relative;
    .bg{
      position: absolute;
      right: 0;
      top:0;
    }
    .van-nav-bar{background: transparent !important;}
  }
  .user_info{
    margin-top:30px;
      padding: 0 15px;
      display: flex;
      .right{
        margin-left: 10px;
        h2{
        font-size:18px;
        font-weight: 500;
        padding-top: 10px;
      }
      .level{
        background: #FFD91A;
        padding:5px 10px 5px 35px;
        font-weight: normal;
        color: #000000;
        display: inline-block;
        border-radius: 30px;
        margin-top: 5px; 
        position: relative;
        img{
          position:absolute;
          left: 5px;
        top: 3px;
        }
      }
       
      }
     
    }
    .cont{
      background: url('../../assets/img/fan/time_cont.png') top no-repeat #FCEEF3;
      background-size: 100%;
      width: 90%;
      margin: 20px auto;
      padding:12px 0 12px 70px;
      box-sizing: border-box;
      span{
        font-size: 12px;
        color: #8A5108;
      }
      h2{
        font-size: 16px;
        color: #8A5108;
        margin-top: 3px;
      }
      button{
        border:none;
        color: #fff;
        background: linear-gradient(to right,#F38843,#FA246F);
        padding: 6px 15px;
        border-radius: 30px;

      }
    }
    .team{
      background: #fff;
      border-radius: 30px;
      padding: 20px;
      h3{
        color: #333333;
        font-size: 15px;
      }
      .box{
        margin-top: 10px;
        position: relative;
        img{
          position: absolute;
          top: 0;
          z-index: 0;
        }
        h1{
          position: relative;
          z-index: 1;
          padding: 5px 15px 10px;
          font-size: 20px;
        }
        span{
          position: relative;
          z-index: 1;
          display: block;
          padding:10px 15px 0;
          font-size: 14px;
        }
      }
    }
    .list{
      padding: 20px;
      li{
        background: rgba(255, 255, 255, 0.9);
        padding:15px 10px;
        border-radius: 5px;
        margin-bottom: 10px;
        span{
          float: right;
        }
      }
    }
  </style>
    
  
  


