import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/home/home'
import Classify from '@/page/home/classify'
import AddressEdit from '@/page/home/addressEdit'
import AddressList from '@/page/home/addressList'
import Index from '@/page/home/index'
import Details from '@/page/home/details'
import List from '@/page/home/list'
import Order from '@/page/home/order'
import Logistics from '@/page/home/logistics'
import User from '@/page/home/user'
import Bill from '@/page/home/bill'
import Notice from '@/page/home/notice'
import Team from '@/page/home/team'
import Invitation from '@/page/home/invitation'
import Paid from '@/page/home/paid'
Vue.use(Router);


//获取原型对象上的push函数
const originalPush = Router.prototype.push
    //修改原型对象中的push方法
Router.prototype.push = function push(location) {  
    return originalPush.call(this, location).catch(err => err)
}

export default new Router({
    routes: [
        {
            path: '/home',
            name: 'home',
            component: Home
        },
        {
            path: '/classify',
            name: 'classify',
            component: Classify
        },
        {
            path: '/addressEdit',
            name: 'addressEdit',
            component: AddressEdit
        },
        {
            path: '/',
            name: 'index',
            component: Index
        },
        {
            path: '/details',
            name: 'details',
            component: Details
        },
        {
            path: '/list',
            name: 'list',
            component: List
        },
        {
            path: '/order',
            name: 'order',
            component: Order
        },
        {
            path: '/logistics',
            name: 'logistics',
            component: Logistics
        },
        {
            path: '/user',
            name: 'user',
            component: User
        },
        {
            path: '/bill',
            name: 'bill',
            component: Bill
        },
        {
            path: '/notice',
            name: 'notice',
            component: Notice
        },
        {
            path: '/team',
            name: 'team',
            component: Team
        },{
            path: '/invitation',
            name: 'invitation',
            component: Invitation
        },{
            path: '/addressList',
            name: 'addressList',
            component: AddressList
        },{
            path: '/paid',
            name: 'paid',
            component: Paid
        },
    ]
})


