<template>
  <div class="user">
    <div class="headr">
      <h1 class="title">个人中心</h1>
      <div class="user_info">
        <img src="../../assets/img/fan/face.png" alt="" width="70">
        <div class="right">
          <h2>{{ protectAddress(account, 8) }}</h2>
          <span class="level"><img src="../../assets/img/fan/j.png" alt="" width="20"> 级别:{{ userInfo ?
              levelFun(userInfo.info[7]) : '--'
          }}</span>
        </div>

      </div>
    </div>
    <div class="zichan">
      <van-row gutter="20" type="flex" justify="start">
        <van-col span="24">
          <div>
            <h1>{{ userInfo ? (Number(userInfo.info[3])+Number(userInfo.info[43])+Number(userInfo.info[32])+Number(userInfo.info[33])+Number(userInfo.info[34]))  : "0" | figure }}</h1>
            <span>累计补贴</span>
          </div>
        </van-col>
      </van-row>
      <van-row gutter="20" type="flex" justify="start">
        <van-col span="12">
          <div>
            <h1>{{ userInfo ? (Number(userInfo.info[3]))  : "0" | figure }}</h1>
            <span>累计静态补贴</span>
          </div>
        </van-col>
        <van-col span="12">
          <div>
            <h1>{{ userInfo ? (Number(userInfo.info[43]))  : "0" | figure }}</h1>
            <span>累计直推补贴</span>
          </div>
        </van-col>
      </van-row>
    </div>

    <div class="order">
      <h2 @click="hreftwo('/order')">我的订单<span>全部订单
          <van-icon name="arrow" />
        </span></h2>
      <div class="order_box">
        <van-row gutter="20" type="flex" justify="space-between">
          <!-- <van-col span="6">
                <div>
                  <img src="../../assets/img/fan/1.png" alt="">
                  <span>待付款 </span>
                </div>
            </van-col> -->
          <van-col span="8" @click="hreftwo('/order')">
            <div>
              <img src="../../assets/img/fan/2.png" alt="">
              <span>已付款</span>
            </div>
          </van-col>
          <van-col span="8" @click="hreftwo('/order')">
            <div>
              <img src="../../assets/img/fan/3.png" alt="">
              <span>待收货</span>
            </div>
          </van-col>
          <van-col span="8" @click="hreftwo('/order')">
            <div>
              <img src="../../assets/img/fan/4.png" alt="">
              <span>已签收</span>
            </div>
          </van-col>
        </van-row>
      </div>

    </div>

    <div class="list">
      <h2>我的服务</h2>
      <div>
        <van-cell title="团 队" icon="friends-o" @click="hreftwo('/team')">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template stor='right-icon'>
            <van-icon name="arrow" />
            <!-- <van-icon name="search" class="search-icon" /> -->
          </template>
        </van-cell>
        <van-cell title="邀 请" icon="share-o" @click="hreftwo('/invitation')">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template stor='right-icon'>
            <van-icon name="arrow" />
            <!-- <van-icon name="search" class="search-icon" /> -->
          </template>
        </van-cell>
        <van-cell title="计 酬" icon="vip-card-o" @click="hreftwo('/paid')">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template stor='right-icon'>
            <van-icon name="arrow" />
            <!-- <van-icon name="search" class="search-icon" /> -->
          </template>
        </van-cell>
        <van-cell title="补 贴" icon="balance-list-o" @click="hreftwo('/bill')">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template stor='right-icon'>
            <van-icon name="arrow" />
            <!-- <van-icon name="search" class="search-icon" /> -->
          </template>
        </van-cell>
        <van-cell title="地 址" icon="guide-o" @click="hreftwo('/addressList')">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template stor='right-icon'>
            <van-icon name="arrow" />
            <!-- <van-icon name="search" class="search-icon" /> -->
          </template>
        </van-cell>
        <van-cell title="公 告" icon="bullhorn-o" @click="hreftwo('/notice')">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template stor='right-icon'>
            <van-icon name="arrow" />
            <!-- <van-icon name="search" class="search-icon" /> -->
          </template>
        </van-cell>
        <van-cell title="客 服" icon="service-o" @click="showTips">
          <template stor='right-icon'>
            <van-icon name="arrow" />
          </template>
        </van-cell>
      </div>
    </div>

    <van-tabbar v-model="active" active-color="#000" inactive-color="#565656">
      <van-tabbar-item @click="hreftwo('/home')">
        <span>首 页</span>
        <template slot="icon">
          <img src="../../assets/img/fan/home1.png" alt="">
        </template>
      </van-tabbar-item>
      <van-tabbar-item @click="hreftwo('/classify')">
        <span>分 类</span>
        <template slot="icon">
          <img src="../../assets/img/fan/lei1.png" alt="">
        </template>
      </van-tabbar-item>
      <van-tabbar-item @click="hreftwo('/user')">
        <span>个人中心</span>
        <template slot="icon" slot-scope="props">
          <img :src="props.active ? icon.active : icon.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
    <van-popup v-model="isFranslation" position="top" :style="{ height: 'auto', top: '10%',background:'transparent' }" :close-on-click-overlay="false">
            <div class="ts">
              <img src="../../assets/img/fan/tips.png" alt="" width="100%">
              <van-icon name="close" color="#fff" size="40px" @click="closTips"/>
            </div>
    </van-popup>
  </div>
</template>
  
<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import data from "../../assets/data.json";
import Web3 from "web3";
import { access } from "fs";
import { userInfo } from "os";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      active: 2,
      timer:null,
      timer2:null,
      isFranslation:false,
      icon: {
        active: 'https://timebabycat.github.io/user.png',
        inactive: 'https://timebabycat.github.io/user1.png',
      },
    };
  },
  computed: {
    ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "userInfo"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      "add_usdtCoinContract",
      "add_buyContract"
    ]),
    levelFun(type) {
      switch (type) {
        case '1':
          return '专员';
        case "2":
          return '主管'
          break;
        case "3":
          return '经理'
          break;
        case "4":
          return '总监'
          break;
        case "5":
          return '总裁'
          break;
        default:
          return '普通会员'
      }
    },
    closTips(){
      this.isFranslation=false
    },
    showTips(){
      this.isFranslation=true
    },
    hreftwo(url) {
      this.$router.push({ path: url });
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },
  },
  created() {
    this.add_usdtCoinContract();
    this.add_buyContract();
  },
  // beforeCreate(){
  //   var h = window.location.hash;
  //   console.log(window)
  //   window.location.href='https://flarenft  .github.io/'+h;
  // },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_buyContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.buyContract || !this.usdtCoinContract) {
          getData();
        } else {

          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.user {
  background: url('../../assets/img/fan/user_bg.png') no-repeat #fff;
  background-size: 100%;

  .title {
    text-align: center;
    font-weight: normal;
    font-size: 18px;
    padding: 15px 0;
    color: #040404;
  }

  .user_info {
    padding: 0 15px;
    display: flex;

    .right {
      margin-left: 10px;

      h2 {
        font-size: 18px;
        font-weight: 500;
        padding-top: 10px;
      }

      .level {
        background: #FFD91A;
        padding: 5px 10px 5px 30px;
        font-weight: bold;
        color: #000000;
        display: inline-block;
        border-radius: 30px;
        margin-top: 5px;
        position: relative;

        img {
          position: absolute;
          left: 2px;
          left: 5px;
          top: 2px;
        }
      }

    }

  }

  .zichan {
    width: 50%;
    margin: 20px 20px 0;
    padding: 15px 0;
    text-align: left;
    border-radius: 5px;
    color: #313131;

    // background: linear-gradient(to right,#ffb3d2,#ff4f96);
    h1 {
      font-size: 20px;
    }

    span {
      display: inline-block;
      width: 100%;
      margin-top: 5px;
      margin-bottom: 15px;
    }
  }

  .order {
    h2 {
      padding: 0 15px;
      font-size: 15px;

      span {
        font-weight: normal;
        float: right;
      }
    }

    .order_box {
      margin: 15px 20px;

      img {
        width: 50px;
        display: block;
        margin: 0 auto 10px;
      }

      span {
        width: 100%;
        display: block;
        text-align: center;
      }
    }
  }

  .list {
    padding-top: 10px;
    padding-bottom: 60px;
    h2 {
      margin: 0 20px 10px;
      font-size: 15px;
    }

  }
}
.ts{
  width: 60%;
  margin: 0 auto;
  text-align: center;
}
</style>
  


