  <script type="es6">
  import HeadTop from "../../components/Header";
  import { mapActions, mapState } from "vuex";
  import { areaList } from '@vant/area-data';
  export default {
    data() {
      return {
        lang: "en",
        currency: "",
        link: "--",
        F5data: {},
      };
    },
    computed: {
      ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "userInfo"
      ]),
    },
    components: {
      HeadTop,
    },
    methods: {
      ...mapActions("main", [
      "add_usdtCoinContract",
      "add_buyContract"
      ]),
      changeAppLocale(locale) {
        this.changeLanguage(locale);
      },
      changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
      onClickLeft(){
        console.log(this.$router)
        this.$router.go(-1)
      },
      getMiningInfoFun: function (num) {
      var _this = this;
      _this.tabsNum = num;
      _this.buyContract.methods
        .getTeamInfo()
        .call()
        .then((result) => {
          console.log(result);
          _this.F5data = result
          // _this.F5data.allBonus = result.ct[11 + num];
          // _this.F5data.balanceBonus = result.ct[7 + num];
          // _this.F5data.vipLength = result.ct[15 + num];
        });
        console.log( _this.F5data)
    },
      onExtract: function () {
      if (this.buyContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.buyContract.methods
        .onExtract()
        .send({ from: account})
        .then((res) => {
          console.log("onExtract", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_buyContract();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    giveBigAward: function () {
      if (this.buyContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.buyContract.methods
        .giveBigAward()
        .send({ from: account})
        .then((res) => {
          console.log("giveBigAward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_buyContract();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    giveSmallAward: function () {
      if (this.buyContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.buyContract.methods
        .giveSmallAward()
        .send({ from: account})
        .then((res) => {
          console.log("giveSmallAward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_buyContract();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    giveAvgAward: function () {
      if (this.buyContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.buyContract.methods
        .giveAvgAward()
        .send({ from: account})
        .then((res) => {
          console.log("giveAvgAward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_buyContract();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
      hreftwo(url) {
        if (url.length > 2) {
          this.$router.push({ path: url + "/?invite=" + this.$route.query.invite });
        } else {
          this.$router.push({ path: url });
        }
      },

  
    },
    mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_buyContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.buyContract || !this.usdtCoinContract) {
          getData();
        } else {
          _this.getMiningInfoFun(1);
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  };
  </script>
    
