  }
}
.my {
  color: #000;
  span{color: #333;}
  button{
 background:linear-gradient(#f38943,#fa236f);
 color: #fff;
 border: none;
 border-radius: 50px;
 font-size: 14px;
 padding: 10px 20px;
 font-weight: normal;
 float: right;
}
}
.textR{text-align: right;}
  </style>
    
  
  


