<template>
  <div class="order">
    <van-nav-bar
  title="邀请有礼"
  left-arrow
  :border="false"
  @click-left="onClickLeft"
/>
    <div class="content">
        <img src="../../assets/img/fan/yq_2.png" alt="" width="100%">
        <div class="box">
          <h1>{{link?link:'--'}}</h1>
          <span>火热进行中，不要错过</span>
          <h4>立即邀请好友参与黄金项目</h4>
          <div class="sy">
            <van-row gutter="5" type="flex" justify="space-between">
              <van-col span="8">
                  <h5>直邀好友</h5>
                  <span>{{ userInfo ? (Number(userInfo.info[11]))  : "0" }}</span>
              </van-col>
              <van-col span="8">
                  <h5>直推补贴</h5>
                  <span>{{ userInfo ? (Number(userInfo.info[43]))  : "0" | figure }}</span>
              </van-col>
              <van-col span="8">
                  <h5>团队业绩(U)</h5>
                  <span>{{ userInfo ? (Number(userInfo.info[23]))  : "0" | figure }}</span>
              </van-col>
            </van-row>
          </div>
          <button  v-clipboard:copy="link"
                v-clipboard:success="onCopy"
                v-clipboard:error="onError">立 即 邀 请</button>
        </div>
        
    </div>
  </div>
</template>
  
<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import { areaList } from '@vant/area-data';
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      tab:0,
      count: 0,
      isLoading: false,
    };
  },
  computed: {
    ...mapState("main", [
    "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "userInfo"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
    "add_usdtCoinContract",
      "add_buyContract"
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
      // 复制成功时的回调函数
      onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/home";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    onClickLeft(){
      console.log(this.$router)
      this.$router.go(-1)
    },
    hreftwo(url) {
      if (url.length > 2) {
        this.$router.push({ path: url + "/?invite=" + this.$route.query.invite });
      } else {
        this.$router.push({ path: url });
      }
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_buyContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.buyContract || !this.usdtCoinContract) {
          getData();
        } else {
          if (_this.userInfo.inviteCode || _this.userInfo.referrer) {
            var invite = _this.userInfo.inviteCode || _this.userInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.order {
  background: url(../../assets/img/fan/bg_yq.png) top center no-repeat #E12872;
    background-size: 100%;
    min-height: 100%;
  .van-nav-bar{
    background:transparent !important;
  }
  .content{
    position: relative;
    margin:350px 20px 0;
    img{
      position: absolute;
      z-index: 0;
    }
    .box{
      position: relative;
      z-index: 1;
      padding:50px 35px;
      text-align: center;
      span{
        display: inline-block;
        margin: 2px 0 5px;
      }
      h1{
        overflow: hidden;text-overflow:ellipsis;white-space: nowrap;
      }
      h4{
        text-align: center;
        margin-bottom:65px;
      }
      h5{
        text-align: center;
        font-weight: normal;
        margin-bottom:8px;
      }
      .sy{
        margin-bottom:50px;
        span{
          display: block;
          text-align: center;
          color: #C6262D;
          font-size: 16px;
          font-weight: bold;
        }
      }
      button{
        border: none;
        background: linear-gradient(#F48B43,#FA266F);
        width: 60%;
        padding: 10px;
        text-align: center;
        font-weight: bold;
        color: #fff;
        font-size: 16px;
        border-radius: 30px;
      }
    }
  }
}

</style>
  




