<template>
  <div class="order">
    <van-nav-bar
  :border=false
  title="团队计酬"
  left-arrow
  @click-left="onClickLeft"
/>
   <div class="my">
    <van-row gutter="20" type="flex" justify="space-between">
      <van-col span="16">
        <h1>{{TotalAward | figure}}</h1>
        <span>第{{ paidData ? paidData[20] : '0' }}期总补贴（USDT）</span>
      </van-col>
      <van-col span="8">
        <button @click="paidFun()">考 核</button>
      </van-col>
    </van-row>
    <van-row gutter="20" type="flex" justify="space-between" v-if="paidData&&paidData[2]==1&&paidData[4]==0" style="margin-top:20px">
      <van-col span="16">
        <h1>{{extract | figure}}</h1>
        <span>可领取补贴（USDT）</span>
      </van-col>
      <van-col span="8">
        <button @click="onExtractFun()">领 取</button>
      </van-col>
    </van-row>
   </div>

   <div class="tab">
    <ul>
      <li :class="tab==0?'in' : ''" @click="changTab(0)">专员</li>
      <li :class="tab==1?'in' : ''" @click="changTab(1)">主管</li>
      <li :class="tab==2?'in' : ''" @click="changTab(2)">经理</li>
      <li :class="tab==3?'in' : ''" @click="changTab(3)">总监</li>
      <li :class="tab==4?'in' : ''" @click="changTab(4)">总裁</li>
    </ul>
    <div class="card">
      <van-row gutter="20" type="flex" justify="space-between">
        <van-col span="24" class="textC">
          <h1>{{list?list[0]:'0' | figure}}</h1>
          <span>平级补贴（USDT）</span>
        </van-col>
      </van-row>
      <van-row gutter="20" type="flex" justify="space-between" >
        <van-col span="12" class="textC">
          <h1>{{list?list[1]:'0' | figure}}</h1>
          <span>大区补贴（USDT）</span>
        </van-col>
        <van-col span="12" class="textC">
          <h1>{{list?list[2]:'0' | figure}}</h1>
          <span>小区补贴（USDT）</span>
        </van-col>
      </van-row>
    </div>
   </div>

   <div class="list">
          <div class="box">我的等级<span>{{paidData ? levelFun(paidData[1]) : '--'}}</span></div>
          <div class="box">考核结果<span>{{paidData ? paidData[2]==0?'未通过': '已通过' :'--'}}</span></div>
          <div class="box">补贴归属<span>{{paidData ? paidData[3]==0?'大区': '小区' :'--'}}</span></div>
          <div class="box">是否领取<span>{{paidData ? paidData[4]==0?'未领取': '已领取' :'--'}}</span></div>
          <div class="box">当前期数<span>{{paidData ? paidData[20] :'--'}} 期</span></div>
          <div class="box">总业绩<span>{{paidData ? paidData[5] : '0' | figure}}USDT</span></div>
          <div class="box">大区业绩<span>{{paidData ? paidData[6] : '0' | figure}} USDT</span></div>
          <div class="box" v-if="paidData &&paidData[2]==0">目标小区业绩<span>{{paidData ?level2Fun(paidData[1]) : '0' | figure}} USDT</span></div>
          <div class="box">小区业绩<span>{{paidData ? paidData[7] : '0' | figure}} USDT</span></div>
          <div class="box">最大线ID<span>{{userInfo ? userInfo.info[18]  : '--' }}</span></div>
          <div class="box">大区补贴<span>{{paidData ? paidData[9] : '0' | figure}} USDT</span></div>
          <div class="box">小区补贴<span>{{paidData ? paidData[10] : '0' | figure}} USDT</span></div>
          <div class="box">平级补贴<span>{{paidData ? paidData[11] : '0' | figure}} USDT</span></div>
          <div class="box">考核倒计时<span> <van-count-down :time="paidData ? paidData[14]*1000 : 0" /></span></div>
        </div>

  </div>
</template>
  
<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import { areaList } from '@vant/area-data';
import notice from "../../assets/notice.json";
export default {
  data() {
    return {
      lang: "en",
      timer:null,
      timer2:null,
      TotalAward:0,
      tab:'0',
      list:[],
      extract:0
    };
  },
  computed: {
    ...mapState("main", ["paidData","paidContract","account","userInfo"]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", ["add_paidContract","add_buyContract"]),
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    level2Fun(type) {
      switch (type) {
        case '1':
          return '300以上';
        case "2":
          return '600以上'
          break;
        case "3":
          return '1000以上'
          break;
        case "4":
          return '20000以上'
          break;
        case "5":
          return '30000以上'
          break;
        default:
          return '不考核'
      }
    },
    levelFun(type) {
      switch (type) {
        case '1':
          return '专员';
        case "2":
          return '主管'
          break;
        case "3":
          return '经理'
          break;
        case "4":
          return '总监'
          break;
        case "5":
          return '总裁'
          break;
        default:
          return '普通会员'
      }
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    onClickLeft(){
      console.log(this.$router)
      this.$router.go(-1)
    },
    hreftwo(url) {
      if (url.length > 2) {
        this.$router.push({ path: url + "/?invite=" + this.$route.query.invite });
      } else {
        this.$router.push({ path: url });
      }
    },
    onExtractFun() {
      if (this.paidContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.paidContract.methods
        .onExtract()
        .send({ from:account})
        .then((res) => {
          _this.$toast.clear();
          _this.add_paidContract();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    paidFun() {
      if (this.paidContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.paidContract.methods
        .checkLoad(account)
        .send({ from:account})
        .then((res) => {
          _this.$toast.clear();
          _this.add_paidContract();
          setTimeout(function(){
            if(_this.paidData[2]==1&&_this.paidData[4]==0){
                if(_this.paidData[3]==0){
                    _this.queryBigAwardfunction(_this.paidData[1],_this.paidData[20]);
                }else{
                  _this.querySmallAwardfunction(_this.paidData[1],_this.paidData[20]);
                }
            }
          },2000)
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    queryBigAwardfunction (num,id) {
      if (this.paidContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
 
      this.paidContract.methods
        .queryBigAward(num,id)
        .call()
        .then((res) => {
          this.extract=res;
          console.log(res);
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    querySmallAwardfunction (num,id) {
      if (this.paidContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
 
      this.paidContract.methods
        .querySmallAward(num,id)
        .call()
        .then((res) => {
          this.extract=res
          console.log(res);
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getTotalAwardfunction () {
      if (this.paidContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
 
      this.paidContract.methods
        .getTotalAward()
        .call()
        .then((res) => {
          this.TotalAward=res
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changTab(num){
     
      if (this.paidContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      _this.tab = num;
      this.paidContract.methods
        .getLevelIncome(num)
        .call()
        .then((res) => {
          this.list=res;
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

    }

  },
  mounted() {
    var _this = this;
    var getData = () => {
      this.add_paidContract();
      this.add_buyContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!_this.account || !_this.paidData || !_this.userInfo) {
          getData();
        } else {
            _this.getTotalAwardfunction();
            _this.changTab(0);
            // setTimeout(function(){
            if(_this.paidData[2]==1&&_this.paidData[4]==0){
                if(_this.paidData[3]==0){
                    _this.queryBigAwardfunction(_this.paidData[1],_this.paidData[20]);
                }else{
                  _this.querySmallAwardfunction(_this.paidData[1],_this.paidData[20]);
                }
            }
          // },2000)
          clearInterval(_this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.order{
  background: url('../../assets/img/fan/notice_bg.png') top no-repeat#fceaf0;
  background-size: 100%;
  min-height: 100%;
  padding-bottom: 20px;
  .van-nav-bar{
      background: transparent !important;
      color: #fff;
  }
}
.tab{
  width: 90%;
  margin: 0 auto;
  text-align: center;
  h1{text-align: center;}
  ul{
    display: flex;
    li{
      width: 20%;
      justify-content: space-between;
      text-align: center;
      padding: 10px 0;
      background: #fafafa;
      border-radius: 10px 10px 0 0;
      color: #666;
    }
    .in{
      background: #ffffff;
      color: #ff3684;
    font-weight: bold;
    }
  }
  .card{
    background: #fff;
    padding: 20px;
    border-radius:  0 0 10px 10px;
  }
}
.my{
  background: #fff;
  padding: 20px ;
box-sizing: border-box;
border-radius: 10px; box-shadow: 0 1px 10px #ddd;
  display: block;
  // justify-content: space-between;
  // grid: 20px;
 margin: 50px auto 20px;
 width: 90%;
 button{
  padding: 10px 30px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    color: #fff;
    background: linear-gradient(45deg, #fcb5d0, #ff76a9);
 }
  h4{
    color: #333;
    font-size: 15px;
    font-weight: normal;
    margin: 0 0 5px;
  }
  span{color:#999}
  .cont{
      width: 85%;
  }
  p{
      margin-top: 10px;
    color: #666;
    font-size: 14px;
    letter-spacing: 1px;
    line-height: 1.5;
    
  }
}
.textR{text-align: right;}
.textC{
  text-align: center;
  margin: 10px 0;
}

.list{
      padding: 20px;
      .box{
        background: rgba(255, 255, 255, 0.9);
        padding:15px 10px;
        border-radius: 5px;
        margin-bottom: 10px;
        span{
          float: right;
        }
      }
    }
</style>
  




