<template>
  <div class="order">
    <van-nav-bar
  title="订单列表"
  left-arrow
  @click-left="onClickLeft"
/>
    <div class="content">
      <div v-if="list.length>0">
            <div class="card"  v-for="itme in list" :key="itme.id" @click="hreftwo('/logistics?id='+itme.goodId)" >
              <van-row gutter="20" type="flex" justify="space-between" >
                <van-col span="8"><img :src="itme.times.insets" alt="" width="" ></van-col>
                <van-col span="16">
                  <div class="box">
                    <h2>{{itme.times.name}} </h2>
                    <h3>{{itme.times.phrase}}</h3>
                    <p style="margin-top:2px"><van-tag  type="danger" v-if="itme.express">已发货</van-tag></p>
                    <h1>${{itme.times.price }} <span>x{{ Number(Number(itme.investAmount)/0.6)/Number(itme.times.price)| price4}}</span></h1>
                  </div>
                </van-col>
              </van-row>
              <div class="info">
                <van-row gutter="20" type="flex" justify="space-end" >
                  <van-col span="24" class="textR" style="margin-top:10px">合计：<span style="color: #ff4a6f;font-size: 18px;">${{Number(itme.investAmount)/0.6 | figure0}}</span></van-col>
                  
                  
                </van-row>
                <van-row gutter="20" type="flex" justify="space-between" >
                  <van-col span="12" style="margin-top:10px">支付方式：线上支付</van-col>
                  <van-col span="12" class="textR">订单编号：{{100000000+Number(itme.orderId)}}</van-col>
                </van-row>
                <p>交易时间：{{itme.investTime | setTime}}</p>
              </div>
          </div>
          </div>
      <!-- <van-tabs v-model="tab" >
        <van-tab title="已付款">
          <div v-if="list.length>0">
            <div class="card"  @click="hreftwo('/logistics')" v-for="(itme,index) in list" :key="index">
              <van-row gutter="20" type="flex" justify="space-between" >
                <van-col span="8"><img :src="itme.times.pictures" alt=""></van-col>
                <van-col span="16">
                  <div class="box">
                    <h2>{{itme.times.name}}</h2>
                    <h3>{{itme.times.phrase}}</h3>
                    <h1>${{itme.times.price }} <span>x{{ Number(itme.investAmount)/Number(itme.times.price) | price4}}</span></h1>
                  </div>
                </van-col>
              </van-row>
              <div class="info">
                      <p >合计：<span style="color: #ff4a6f;font-size: 18px;">${{itme.investAmount | figure}}</span></p>
                      <p>支付方式：线上支付</p>
                      <p>订单编号：{{itme.investTime}}</p>
                    </div>
          </div>
          </div>
          
        </van-tab>
        <van-tab title="待收货">待收货</van-tab>
        <van-tab title="已签收">已签收</van-tab>
      </van-tabs> -->
      <div class="box">
      </div>

    </div>
  </div>
</template>
  
<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import data from "../../assets/data.json";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      tab:0,
      count: 0,
      isLoading: false,
      list:[],
      oldlist:[],
      logistics:[]
    };
  },
  computed: {
    ...mapState("main", [
    "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "userInfo"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
    "add_usdtCoinContract",
      "add_buyContract"
    ]),
            // 复制成功时的回调函数
            onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    onClickLeft(){
      console.log(this.$router)
      this.$router.go(-1)
    },
    hreftwo(url) {
      this.$router.push({ path: url });
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    getList() {
       if (this.userInfo == null) {
        return;
      }
     var _this = this;
			_this.oldlist = [];
			var investsLength = _this.userInfo.info[13];
			var times = parseInt(investsLength) - 1; 
      var account = _this.account;
      
      for (let i = times; i > -1; i--) {
        
      this.buyContract.methods
        .getInvestInfo(
          account,
          i
        )
        .call()
        .then((res) => {
          	//prop tips
							var investAmount = res[1];	//投资的金额
							var limitAmount = res[2];	//预计收益
							var earnAmount = res[3];	//当前收益
							var investTime = res[4];	
              var time = res[5];	//投资时间
              var orderId = res[6];	//订单id
              var goodId = res[7];	//商品id
              if(i==0){
                earnAmount=Number(earnAmount)-Number(_this.userInfo.info[3])
              }
             if(investAmount>0 ){
              _this.$axios.get('https://timenftbsc.github.io/goods.json').then(
                  function(obj){
                    var good = obj.data;
                   
                    for (let j = good.length-1; j > -1; j--) {
                      if(good[j].id==goodId){
                        // console.log(_this.logistics.length);
                          // if(_this.logistics.length>0){

                          //   for (let m = _this.logistics.length-1; m > -1; m--) {
                          //       console.log(m)
                          //     }

                          // }else{
                            _this.oldlist.push({
                                id: i,
                                investAmount: investAmount,
                                earnAmount: earnAmount,
                                limitAmount: limitAmount,
                                goodId:goodId,
                                investTime:investTime,
                                times:good[j],
                                orderId:orderId,
                              });
                          // }
                       
                      
                          
                      }
                          }
                    
                  }
                ).catch(
                  function(err){
                    console.log(err)
                  }
                )

                
              }else{
                _this.oldlist=[];
              }

                  
          	_this.oldlist.sort(_this.sortNumber);
            
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

			
			}
    },
    fahuo : async function() {
      var _this = this;
       _this.getList();
       await _this.$axios.get('https://timenftbsc.github.io/user/0x6f48ebec876e5d3a587cdded952b785ae379453e.json').then(
            function(res){
              _this.logistics =res.data;
              alert(_this.list.length)
              for (let j = _this.logistics.length; j > -1; j--) {
                    alert(j);
                }
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )
        
    }
  },
  async mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_buyContract();
      this.add_usdtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!_this.buyContract || !_this.usdtCoinContract || !_this.userInfo) {
          getData();
        } else {
          
          _this.getList();
          // _this.fahuo();
          _this.$axios.get('https://timenftbsc.github.io/user/'+ _this.account +'.json').then(
            function(res){
              _this.logistics =res.data;
            
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )

          setTimeout(function(){
           
             if(_this.oldlist.length>0 && _this.logistics.length>0){
              _this.list=[];
              for (let m = _this.logistics.length-1; m > -1; m--) {
                for (let j = _this.oldlist.length-1; j > -1; j--) {
                    if(Number(_this.oldlist[j].orderId)+100000000 == Number(_this.logistics[m].orderNumber)){
                    _this.oldlist[j]['express'] = _this.logistics[m].express;
                    _this.list.push(_this.oldlist[j]);
                      }
                  }
                 
                } 
                if(_this.list.length < _this.oldlist.length){
                  for (let m = _this.oldlist.length-1; m > -1; m--) {
                  for (let j = _this.list.length-1; j > -1; j--) {
                      if(Number(_this.oldlist[m].orderId) == Number(_this.list[j].orderId)){
                        _this.oldlist[m]=_this.list[j]
                        }
                    }
                  
                  } 
                  _this.list = _this.oldlist;
                }
              }else{
                _this.list=[];
                _this.list=_this.oldlist;
              }
          },2000)
         

  



          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.content {
  padding-top: 1px;
  .van-tab{
    background: #fff;
  }
  .van-card{
    background: #fff !important;
    padding:20px 16px !important;
    margin-top: 10px;
  }
  .goods{
    p{
       background: #fff;
    border-top: 1px solid #f5f5f5;
    padding: 15px 16px;
    color: #999999;
    span{
      float: right;
      color:#000;
    }
    }
   .mini{
    padding: 15px 16px;
    background: #fff;
    margin-top:10px;
    text-align: right;
    color: #999999;
    span{
      float: right;
      color:#000;
      font-size: 16px;
      margin-left: 10px;
    }
   }
  }
}
.card{ 
  background: #fff;
  padding: 10px;
  margin-top: 10px;
  img{width: 100%;border-radius: 5px;}
  .box{
   
    h2{
      font-size: 15px;
      font-weight: normal;
      color:#333333;
      padding-top: 10px;
    }
    h3{
      font-size: 14px;
      font-weight: normal;
      color:#999;
      padding-top: 5px;
    }
    h1{
      font-size: 18px;
      font-weight: normal;
      color:#ff4a6f;
      padding-top: 10px;
      span{
        float: right;
        font-size: 14px;
        color:#999;
      }
    }
  }
  .textR{text-align: right;margin-top: 10px;}
  .info{
    border-top: 1px solid #f5f5f5;
    padding: 10px;
    margin-top: 5px;
    p{
    font-size: 14px;
    color:#333;
    text-align: right;
    margin-top: 5px;
  }
  } 
}
</style>
  




