<template>
  <div class="details">
    <van-nav-bar
  title="确认订单"
  left-arrow
  @click-left="onClickLeft"
/>
    <div class="content">
      

      <div class="address">
        <div class="addr" v-if="AddressInfo">
          <van-row type="flex" justify="space-between" align="center">
            <van-col span="20" > 
        <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="6"><h4>收货人</h4></van-col>
            <van-col span="18">
              <div class="box">
                <h3>{{AddressInfo.name}}</h3>
                <h3>{{AddressInfo.tel}}</h3>
              </div>
            </van-col>
          </van-row>
          <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="6"><h4>收货地址</h4></van-col>
            <van-col span="18">
              <div class="box">
                <h3>{{AddressInfo.province}}{{AddressInfo.county}}{{AddressInfo.city}}{{AddressInfo.addressDetail}}</h3>
              </div>
            </van-col>
          </van-row></van-col>
            <van-col span="4" style="    text-align: right;"> <van-icon name="arrow" @click="hreftwo('/addressList')"/></van-col>
          </van-row>
       
      </div>

      <!-- <div v-else class="noAddr" @click="hreftwo('/addressEdit')">您还没有收货地址，点击此处立即添加！</div>
        <van-row type="flex" justify="space-between" v-if="list">
        <van-col span="20">
          <div class="xq">
            <h4><van-icon name="guide-o" /> {{list.name}}  <span>{{list.tel}}</span></h4>
            <p>{{list.address}}</p>
          </div>
        </van-col>
        <van-col span="4" style="text-align: right;">
          <van-icon name="arrow" @click="hreftwo('/addressList')"/>
        </van-col>
      </van-row>
      <div v-else class="noAddr" @click="hreftwo('/addressEdit')">您还没有收货地址，点击此处立即添加！</div> -->
      </div>
      <img src="../../assets/img/goods/line.png" alt="" width="100%">
      <div class="goods" v-if="goods">
        <div class="card"  @click="hreftwo('/logistics')">
              <van-row gutter="20" type="flex" justify="space-between" >
                <van-col span="8" >
                  <img :src="goods.insets" alt="" width="" >
                </van-col>
                <van-col span="16">
                  <div class="box">
                    <h2>{{goods?goods.name:'--'}}</h2>
                    <h3>{{goods?goods.phrase:'--'}}</h3>
                    <h1>${{ goods?goods.price:0}} <span>x{{ num  }}</span></h1>
                  </div>
                </van-col>
              </van-row>
          </div>
          <p>支付币种 <span>USDT</span> </p>
      <p>支付方式 <span>在线支付</span> </p>
      <p>运费 <span>免运费</span> </p>
      <div class="mini">
        共{{ num  }}件 <span>小计：${{goods?goods.price*num:0}}</span> 
      </div>
      </div>
      <div v-if="isApprove">
        <van-submit-bar v-if="goods" decimal-length="2" :price="goods.price*100*num" button-text="确 定" button-color="#ff1773" @submit="onSubmit" currency="$"/>
      </div>
      <div v-else>
        <van-submit-bar v-if="goods" decimal-length="2" :price="goods.price*100*num" button-text="授 权" button-color="#000" @submit="usdtApprove" currency="$"/>
      </div>
      
    </div>


  </div>
</template>
  
<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
import data from "../../assets/data.json";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      list:{},
      goods:null,
      show: false,
      isApprove:false,
      timer:null,
      AddressInfo:{}
    };
  },
  computed: {
    ...mapState("main", [
      "usdtCoinContract",
      "buyContract",
      "account",
      "usdtBalance",
      "getListContract",
      "listData",
      "userInfo"
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      "add_usdtCoinContract",
      "add_buyContract",
      "add_getListContract"
    ]),
    // 提交商品
    onSubmit(){
      if (this.buyContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      if(!this.AddressInfo || !this.AddressInfo.name){
        this.$toast('请添加收货地址！');
        return
      }
      var  referrer = this.userInfo.referrer?this.userInfo.referrer:localStorage.getItem("invite");
      if(!referrer || referrer=='undefined' || referrer==null){
        this.$toast('没有邀请人不能购买！');
        return
      }
      var money = (this.num*this.goods.price).toString();
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(this.goods)
      this.buyContract.methods
        .investIn(account, referrer,web3.utils.toWei(money, 'ether'),this.goods.id,this.AddressInfo.id)
        .send({
          from: account,
        })
        .then((res) => {
          // console.log('www',res)
          this.$toast.success('购买成功！');
          this.$toast.clear();
          this.hreftwo('/order');
        });

      // var params ={"bean":{"buyQuantity":this.num,"productId":this.goods.id,"userAddressId":this.list.id,},"optType":"save"}
      
      // var token = localStorage.getItem('accessToken');
      //   if(token){
      //        // 创建订单
      //       this.$axios.post('http://42.194.207.81:38080/linkstop/client/link/order/create',params,{
      //         headers:{
      //           "Authorization":token
      //         }
      //       }).then(
      //       function(res){
      //         var data = res.data;
      //         if(data.success==true){

      //           // _that.$toast.success('成功！');
      //           // _that.hreftwo('/addressList');
      //         }
      //         console.log(data)
             
      //       }
      //     ).catch(
      //       function(err){
      //         console.log(err)
      //       }
      //     )
           
      //     }

    },
    onConfirm(values) {
      this.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/');
      this.showArea = false;
    },
    onClickLeft(){
      this.$router.go(-1)
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    hreftwo(url) {
      this.$router.push({ path: url });

      // }
    },
    onEdit() {
      this.hreftwo('/addressList');
    },
        // USDT授权
   usdtApprove: function () {
      if (this.usdtCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = '0xca2b54858faffd333b866ec45a94dc9ee181f7ea';
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.usdtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
   // 是否授权
   isApproveFun: function () {
      var account = this.account;
      var address = '0xca2b54858faffd333b866ec45a94dc9ee181f7ea';

      this.usdtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun', res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
  },
  mounted() {
    var _this = this;
    if (this.$route.query.id) {
      this.$axios.get('https://timenftbsc.github.io/goods.json').then(
            function(res){
              var good= res.data;
              good.forEach(element => {
            if(element.id==_this.$route.query.id){
              _this.goods =element
            }
        });
            }
          ).catch(
            function(err){
              console.log(err)
            }
          )
          
     }else{
      this.$router.push({ path: '/home' });
     }

     
     if (window.location.hash.indexOf('num')>0) {
        this.num = window.location.hash.split('=')[2];
      }
      // var token = localStorage.getItem('accessToken');
      //   if(token){
      //        // 查询地址列表接口
      //       this.$axios.post('http://42.194.207.81:38080/linkstop/client/link/user/address/list',{},{
      //         headers:{
      //           "Authorization":token
      //         }
      //       }).then(
      //       function(res){
      //         var data = res.data
      //         if(data.success==true){
                
      //           if(data.result.result.length>1){
      //             var list=data.result.result;
      //             list.forEach(element => {
      //               var xq = element.detailAddress.split(',');
      //               var arr = {
      //                 id: element.id,
      //                 name: element.name,
      //                 tel: element.phone,
      //                 address: xq[1]+xq[2]+xq[3]+xq[4],
      //                 isDefault: element.status==1?true:false,
      //               }
      //               if(element.status==1){
      //                 _this.chosenAddressId = element.id;
      //                 _this.list=arr;
      //               }
                   
      //             });
      //           }
      //         }
      //         // console.log(data)
      //       }
      //     ).catch(
      //       function(err){
      //         console.log(err)
      //       }
      //     )
           
      //     }
    
 var getData = () => {
      this.add_usdtCoinContract();
      this.add_buyContract();
      this.add_getListContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.buyContract || !this.usdtCoinContract || !this.getListContract || !_this.listData) {
          getData();
        } else {
          this.isApproveFun();
          _this.listData.forEach((element,index) => {
            var lists=element.split(",");
            if(lists[8]==1 && lists[7]=='1'){
              _this.chosenAddressId = lists[7]=='1'? lists[0]:'0';
              _this.AddressInfo={
                addressDetail: lists[4],
                areaCode: lists[0],
                city: lists[3],
                county:lists[2],
                name: lists[5],
                province:lists[1],
                tel: lists[6],
                id:index,
                isDefault:lists[7]==1?true:false,
              }
            }
            
            
          });

          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();


  },
  destroyed() {

  },
};
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.content {

  background: #fff;
    .van-field__label{
    color: #000;

  }
  .van-card{
    background: #fff !important;
    padding:20px 16px !important;
  }
  .goods{
    margin-top:10px;
    p{
       background: #fff;
    border-top: 1px solid #f5f5f5;
    padding: 15px 16px;
    color: #999999;
    span{
      float: right;
      color:#000;
    }
    }
   .mini{
    padding: 15px 16px;
    background: #fff;
    margin-top:10px;
    text-align: right;
    color: #999999;
    span{
      float: right;
      color:#000;
      font-size: 16px;
      margin-left: 10px;
    }
   }
  }
}
.address{
  
  .addr{
    // margin:0 20px;
    // background: url('../../assets/img/goods/line.png') bottom no-repeat #fff;
    // background-size: 100%;
    padding:10px 20px 20px 20px;
    h3{
      font-size: 14px;
      color:#000;
      font-weight: normal;
      margin-top: 10px;
    }
    h4{
      font-size: 14px;
      color:#999999;
      font-weight: normal;
      margin-top: 10px;
    }
  }
}
.card{ 
  background: #fff;
  padding: 10px;
  margin-top: 10px;
  img{width: 100%;border-radius: 5px;}
  .box{
   
    h2{
      font-size: 16px;
      font-weight: normal;
      color:#333333;
      padding-top: 10px;
    }
    h3{
      font-size: 14px;
      font-weight: normal;
      color:#999;
      padding-top: 5px;
    }
    h1{
      font-size: 18px;
      font-weight: normal;
      color:#ff4a6f;
      padding-top: 20px;
      span{
        float: right;
        font-size: 14px;
        color:#999;
      }
    }
  }
  .info{
    border-top: 1px solid #f5f5f5;
    padding: 10px;
    margin-top: 5px;
    p{
    font-size: 15px;
    color:#333;
    text-align: right;
    margin-top: 5px;
  }
  } 
}
.noAddr{
  text-align: center;
  color: #6973ff;
}
</style>
  


